.root {
    height: 38px;
    border-radius: 10px;
    background-color: #f7f7fa;
    display: flex;
    align-items: center;
    padding: 0 16px;
    cursor: pointer;

    .changeLabel {
        margin-left: auto;
        font-size: 14px;
        color: #007aff;
        user-select: none;
    }
}

.currencyBadgeWrapper {
    flex: 0 0 90px;
    display: flex;
    justify-content: space-between;

    &.lax {
        flex: 0;

        .currencyCode {
            margin-left: 8px;
        }
    }
}

.currencySeparator {
    margin: 0 4px;
}
