.root {
    display: block;
    position: relative;
    padding: 16px 38px 16px 16px;
    border-radius: 8px;
    background-color: #fff;
    color: #333;
    box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.08);

    &:active {
        color: #333;
    }

    &.container {
        display: flex;
        flex-direction: row;
    }

    .wrapper {
        overflow: hidden;
    }
}

.title {
    display: block;
    width: 100%;
    font-weight: bold;
    font-size: 20px;
    line-height: 26px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.url {
    display: block;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: #999;
}

.chevron {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 38px;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

.status {
    display: inline-block;
    font-weight: normal;
    font-size: 12px;
    line-height: 24px;
    color: #777;
    padding: 0 8px;
    margin: 6px 0 0 0;
    border-radius: 4px;
    background-color: #f0f0f0;
}
