@import '@mssgme/scss-basics/vars';

$sideways-offset: 100px;
$accent-size: 62px;

.root {
    width: 100%;
    height: var(--canvas-dashboard-height);
    position: absolute;
    bottom: var(--canvas-dashboard-padding-bottom);
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    z-index: 10;
    transition: width $default-transition;

    .content {
        width: 100%;
        height: 100%;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        margin: 0 16px;
    }

    .row {
        transform: translateY(-100%);
        width: 100%;
        height: 100%;
        overflow: hidden;
        display: flex;
        flex: 0 0 100%;
        justify-content: center;
        transition: transform 0.3s $default-transition;

        &.toolbar {
            overflow-x: scroll;
            scrollbar-width: none; /* Firefox */
            -ms-overflow-style: none;  /* Internet Explorer 10+ */

            &::-webkit-scrollbar { /* WebKit */
                width: 0;
                height: 0;
            }
        }
    }

    &.focusMode {
        box-shadow: none;

        .row {
            transform: translateY(-200%);
        }

        .addButton {
            background-color: #fff;
            transform: rotate(135deg);
            box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.08);

            > a {
                color: #333;
            }
        }

        &.onlyFirstRow {
            width: $accent-size;

            .content {
                opacity: 0;
            }

            .row {
                transform: none;
            }
        }
    }

    &.sideways {
        .row {
            transform: translateY(0);
            opacity: 0;
        }

        .content {
            padding-left: $sideways-offset;
            margin-right: 0;
        }

        .addButton {
            background-color: #fff;
            box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.08);
            transform: translateX(-160px) rotate(-135deg);

            @media all and (max-width: 600px) {
                transform: translateX(calc(-50vw + 70px)) rotate(135deg);
            }

            > a {
                color: #333;
            }
        }
    }

    .row.padLeft {
        opacity: 1;
        justify-content: left;
    }

    .row.padRight {
        opacity: 1;
        justify-content: right;
    }

    .row.noSelection {
        opacity: 0;
    }

    .divider {
        border-left: 1px solid #ddd;
        margin: 12px 10px 12px 0;
        position: sticky;
        left: 0;
        top: 0;
    }
}

.addButton {
    box-sizing: border-box;
    position: absolute;
    top: -4px;
    left: calc(50% - #{$accent-size} / 2);
    border-radius: 50%;
    background-color: #007aff;
    cursor: pointer;
    border: 3px solid #fff;
    transition: transform 0.3s ease, background-color 0.3s ease, box-shadow 0.3s ease;

    > a {
        $size: $accent-size - 3px * 2;
        display: flex;
        justify-content: center;
        align-items: center;
        width: $size;
        height: $size;
        color: #fff;

        svg {
            transition: color 0.3s ease;
        }
    }

    &.disabled {
        pointer-events: none;
        opacity: 0.5;
        background-color: $placeholder-color;

        > a {
            pointer-events: none;
        }
    }
}

.placeholder {
    visibility: hidden;
    position: unset;
}
