.root {
    .toggleContainer {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: 0 0 16px 0;
    }

    .publishedLabel {
        display: block;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        position: relative;

        &.builderLabel {
            padding-left: 10px;
            color: #333;
            margin-bottom: 4px;
        }
    }

    .currencyBadge {
        display: flex;
        align-items: center;
        width: 74px;
        flex-shrink: 0;
        background-image: none;

        &.disabled {
            opacity: 0.6;
            pointer-events: none;
            padding: 0 16px;
            width: 66px;
            background-image: none;
        }
    }
}
