@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.row {
    width: 100%;
    display: flex;
    align-items: center;
    background-color: #f7f7fa;
    padding: 0 12px 0 16px;
    cursor: pointer;
    user-select: none;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.5;
    position: relative;
    overflow: hidden;
    height: 38px;
    border-radius: 10px;

    .left {
        display: flex;
        align-items: center;
        flex-shrink: 0;
        position: relative;
        z-index: 1;
        height: 100%;
    }

    .value {
        width: 100%;
        padding: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        color: $placeholder-color;
    }

    .buttons {
        display: flex;
        height: 100%;
        padding: 6px 0;
        flex-shrink: 0;
        align-items: center;
        position: relative;
        z-index: 1;

        .button {
            display: flex;
            height: 100%;
            justify-content: center;
            align-items: center;
            overflow: hidden;
            position: relative;
            border-radius: 6px;
            font-weight: 500;
            font-size: 14px;
            line-height: 14px;
            color: $placeholder-color;
            cursor: pointer;
            transition: color 0.3s ease;
            //opacity: 0.3;

            &.icon {
                padding: 0;
            }
        }
    }

    &.active {
        .value {
            color: #333;
        }

        .button {
            opacity: 1;
        }
    }
}

.sortable {
    height: 38px !important;
}
