.root {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    list-style: none;
    //background-color: #f5f5f5;
    //border-radius: 10px;
    //padding: 6px 16px 12px 16px;
    padding: 0;

    .disabled {
        pointer-events: none;
        opacity: 0.8;
    }

    .addPhotoBtn {
        width: 100%;
        padding-top: 100%;
        position: relative;
        background-color: #e9e9e9;
        border-radius: 10px;
        overflow: hidden;
        cursor: pointer;
    }

    .addPhotoBtnInput {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        cursor: pointer;
        opacity: 0;
        z-index: 1;
    }

    .addPhotoBtnContent {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .removePhotoBtn {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        overflow: hidden;
        background-color: #f9f9f9;
        cursor: pointer;
        position: absolute;
        top: 0;
        right: -10px;
        border: 1px solid #ddd;

        > svg {
            transform: rotate(45deg);
            pointer-events: none;
        }
    }
}

.imagePreview {
    width: calc(25% - 10px);
    margin: 0 13px 3px 0;
    padding: 10px 0 0 0;
    position: relative;
    list-style: none;
    cursor: grab;
    pointer-events: auto !important;

    &.sortable {
        cursor: grabbing;

        .removePhotoBtn,
        .mainImageIcon {
            display: none;
        }
    }

    &:nth-of-type(4n) {
        margin: 0 0 3px 0;
    }

    .imagePreviewContainer {
        width: 100%;
        padding-top: 100%;
        position: relative;

        > img {
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 10px;
        }
    }

    .mainImageIcon {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        pointer-events: none;
        padding: 10px 0 0 0;
    }
}
