.root {
    background-color: #fff;
    min-height: 100%;
}

.topPanel {
    padding: 20px 16px 16px 16px;
    background-color: #f7f7fa;
    display: flex;
    flex-direction: column;
}

.header {
    display: flex;
    align-items: center;
    width: 100%;
    margin: 0 0 16px 0;

    .backLink {
        display: block;
        height: 24px;
        flex-shrink: 0;
    }

    .title {
        font-size: 34px;
        line-height: 1;
        margin: 0 0 0 16px;
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        flex: 1;
        white-space: nowrap;
    }
}

.preview {
    margin: 0 auto;
    width: 30%;
}
