.loadingContainer,
.error {
    width: 100%;
    height: 150px;
    display: flex;
    justify-content: center;
}

.loadingContainer {
    align-items: center;
}

.order {
    border-radius: 12px;
    background-color: #F7F7FA;
    margin: 0 0 16px 0;
}

.orderList {
    list-style: none;
    margin: 0;
    padding: 6px 16px;
    font-size: 16px;

    > li {
        display: flex;
        width: 100%;
        justify-content: space-between;
        position: relative;
        padding: 10px 0;

        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 1px;
            position: absolute;
            bottom: -1px;
            left: 0;
            background-color: #eee;
        }

        &:last-of-type::after {
            display: none;
        }

        > span:last-of-type {
            font-weight: 600;
        }
    }
}

.error {
    color: #f00;
}
