.root {
    width: 375px;
    height: 700px;
    max-width: 100%;
    max-height: 100%;
    overflow: hidden;
    position: relative;
    background-color: #fff;
    border-radius: 16px;
    box-shadow: 0 2px 30px rgba(0, 0, 0, 0.08);
    border: 1px solid #eee;
}

.content {
    padding: 30px 25px;
}

.cover {
    height: 170px;
    background-color: #f2f2f2;
}

.line {
    max-width: 100%;
    height: 18px;
    background-color: #f2f2f2;
    border-radius: 100px;
    margin: 0 0 18px 0;
}

.image {
    border-radius: 16px;
    background-color: #f2f2f2;
    height: 200px;
    margin: 30px 0 0 0;
}

.messengers {
    margin: 30px 0 22px 0;
    display: flex;
    flex-wrap: wrap;

    > div {
        width: calc(33% - 5px);
        height: 36px;
        margin: 0 8px 8px 0;
        background-color: #f2f2f2;
        border-radius: 10px;

        &:nth-child(3n) {
            margin: 0 0 8px 0;
        }
    }
}

.modalContainer {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.1);
    opacity: 0;
    animation: revealLayer 1s ease forwards;
    animation-delay: 1s;
}

.modal {
    width: calc(100% - 80px);
    border-radius: 16px;
    background-color: #fff;
    padding: 25px 16px 16px 16px;
    position: absolute;
    top: calc(50%);
    left: calc(50%);
    transform: translate(-50%, 0);
    box-shadow: 0 2px 30px rgba(0, 0, 0, 0.08);
    opacity: 0;
    animation: revealModal 1s ease forwards;
    animation-delay: 1.6s;
    color: #333;
}

.modalHeading {
    display: block;
    font-weight: 600;
    font-size: 20px;
    margin: 0 0 10px 0;
}

.modalContent {
    font-size: 14px;
    line-height: 1.5;
    color: #777;
    margin: 0;
}

@keyframes revealLayer {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes revealModal {
    0% {
        opacity: 0;
        transform: translate(-50%, 0);
    }
    100% {
        opacity: 1;
        transform: translate(-50%, -50%);
    }
}
