.root {
    width: 200px;
    max-width: 100%;
    margin: 0 auto;
    border-radius: 10px;
}

.preview {
    width: 100%;
    position: relative;
    padding-top: 186%;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 1.5px 20px 0 rgba(0, 0, 0, 0.08);
}

.innerContent {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.imageContainer {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;

    > img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.actionsContainer {
    width: 100%;
    padding: 10px;
}

.fileInput {
    opacity: 0;
    display: block;
    width: 375px;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    cursor: pointer;

    * {
        cursor: pointer;
    }
}

.opacityBlock {
    width: 100%;
    height: 100px;
    position: absolute;
    top: 0;
    left: 0;
    padding: 16px 16px 0 16px;
    z-index: 1;
    text-align: center;
    color: #fff;
    font-size: 14px;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
