@import '@mssgme/scss-basics/vars';

.root {
    --shadow-0: none;
    --shadow-1: 0 4px 8px rgba(0, 0, 0, 0.04), 0 0 2px rgba(0, 0, 0, 0.06), 0 0 1px rgba(0, 0, 0, 0.04);
    
    --bg-width: calc(100% - 80px);
    --bg-top: var(--canvas-bg-top);
    --bg-bottom: var(--canvas-bg-bottom);
    --bg-shadow: var(--shadow-1);

    --content-top: var(--canvas-bg-top);
    --content-bottom: var(--canvas-bg-bottom);

    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.5;
    color: #333;
}

.viewMode {
    --bg-width: 100%;
    --bg-top: 0;
    --bg-bottom: 0;
    --bg-shadow: var(--shadow-0);

    --content-top: var(--safe-area-inset-top);

    --burger-top: var(--preview-burger-top);
}

.bgColor {
    position: absolute;
    top: var(--bg-top);
    bottom: var(--bg-bottom);
    left: 50%;
    transform: translateX(-50%);
    border-radius: var(--canvas-border-radius);

    width: var(--bg-width);
    transition: width $scale-transition, bottom $scale-transition, top $scale-transition, box-shadow $scale-transition;
    will-change: width, bottom, top, box-shadow;

    box-shadow: var(--bg-shadow);

    @media all and (max-width: $mobile-breakpoint) {
        border-radius: 0;
    }
}

.bgImage {
    > img {
        overflow: hidden;
    }
}

.layout {
    width: 100%;
    height: 100%;
    z-index: 1;
}

.contentWrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.content {
    width: 100%;
    position: absolute;
    top: var(--content-top);
    left: 0;
    bottom: var(--content-bottom);
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    scroll-behavior: smooth;

    transition: top $scale-transition, bottom $scale-transition;
    will-change: top, bottom;
}

.contentInner {
    width: 600px;
    max-width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    border-radius: var(--canvas-border-radius);
    margin: 0 auto;
}

.content::-webkit-scrollbar {
    width: 0;
}

.content::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0);
}

.content::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0);
}

.content:hover::-webkit-scrollbar-thumb {
    background: rgba(200, 200, 200, 0.5);
}

.emptyPage {
    padding: 16px 56px !important;
}

.actionsContainer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    pointer-events: none;
}

.actions {
    width: 600px;
    max-width: 100%;
    margin: 0 auto;
    position: relative;

    > * {
        pointer-events: auto;
    }
}
