.root {

}

.element {
    display: flex;
    place-items: stretch;

    > * {
        pointer-events: none;
    }
}

.area {
    pointer-events: none;
    position: absolute;
    background: rgba(0, 0, 0, 0.6);
    width: 100%;
    height: 100%;
    z-index: 500;

    .marker {
        position: absolute;
        background: #007aff;
        border: 0 dotted #007aff;
    }

    .line {
        position: absolute;
        border: 0 dotted #007aff;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;

        &.h {
            border-top-width: 1px;
            height: 1px;
        }

        &.v {
            border-left-width: 1px;
            width: 1px;
        }
    }

    .portal {
        display: flex;
        place-items: center;
        place-content: center;
    }
}
