.root {
    width: 100%;
    padding-bottom: 16px;

    border-radius: 10px;
    box-shadow: 0 1px 3px rgba(255, 0, 0, 0.1);
    overflow: hidden;

    > * {
        padding: 0 16px;
    }

    .heading {
        padding: 16px;
        font-size: 16px;
        font-weight: bold;
        width: 100%;
        background: rgba(255, 0, 0, 1);
        color: white;
    }

    .reason {
        margin-top: 16px;
        font-size: 14px;

        .reasonHeading {
            font-weight: bold;
            margin-right: 5px;
        }

        .reasonText {

        }
    }

    .appeal {
        margin-top: 16px;
        font-size: 14px;
    }

    .pending {
        margin-top: 16px;
        font-size: 12px;
        color: gray;
    }
}
