.link {
    margin: 12px 0 8px 0;
    height: 44px;
    background: #fff;
    border-radius: 8px;
    font-size: 14px;
    line-height: 24px;
    box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.08);
    transition: box-shadow 0.6s ease, background-color 0.6s ease;
}

.tag {
    color: #333;
    font-weight: 400;
}

.personalLink {
    margin-top: 16px;
}
