.root {
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
    width: calc(100% + 32px);
    margin: -16px 0 0 -16px;
    padding: 0;
    border-bottom: 1px solid #f2f2f2;
}

.content {
    width: 100%;
    max-height: 400px;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}

.content::-webkit-scrollbar {
    width: 4px;
}

.content::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0);
}

.content::-webkit-scrollbar-thumb {
    background: #d3d3d3;
}

.backgroundWrapper {
    width: calc(100% + 32px);
    padding-top: 186%;
    position: absolute;
    top: 50%;
    left: -16px;
    transform: translateY(-50%);
}
