@import '@mssgme/scss-basics/vars';

.dateSection {
    position: sticky;
    text-align: center;
    font-weight: bold;
    padding: 6px 0;
}

.emptyMessage {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 16px;
    text-align: center;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.action {
    color: #eee;

    &.new {
        background-color: $status-new;
    }

    &.processing {
        background-color: $status-processing;
    }

    &.accepted {
        background-color: $status-accepted;
    }

    &.rejected {
        background-color: $status-rejected;
    }

    &.delete {
        background-color: $danger;
    }
}
