.flexForm {
    display: flex;
    flex-direction: column;
    min-height: 0;
    flex: 1;
}

.saveButton {
    padding-right: 16px;
    height: 100%;
    font-size: 16px;
}

.menuIcon {
    display: flex;
    width: 60px;
    height: 100%;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.menuWrapper {
    margin: -16px 0;
    list-style: none;
    padding: 0;
}

.menuListItem {
    margin: 16px 0;
}

.menuItem {
    cursor: pointer;
}

.disabled {
    opacity: 0.4;
    pointer-events: none;
}
