.list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    height: auto;
}

.container {
    display: flex;
    flex-direction: column;
    margin: 0 0 9px 0;

    &.deleting {
        height: 0 !important;
        margin: 0;
        overflow: hidden;
        opacity: 0;
        transition: opacity 0.1s ease-out, height 0.05s 0.3s ease-out, margin 0.05s 0.1s ease-out;
    }

    &:last-of-type {
        margin: 0;
    }
}

.item {
    position: relative;

    .actions {
        user-select: none;
        position: absolute;
        z-index: 0;
        top: 0;
        height: 100%;
        display: flex;
        flex-direction: row;
        font-size: 80%;

        > div {
            display: flex;
            flex-direction: row;
            align-items: center;

            > * {
                margin: 10px;
                padding: 8px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 4px;
                transition: opacity 0.3s ease, transform 0.1s ease;
                flex-direction: column;
                font-size: 80%;

                & + * {
                    margin-left: 0;
                }

                > svg {
                    color: inherit;
                }
            }
        }

        &.hidden {
            display: none;

            & > div > * {
                transform: scale(0.9);
            }
        }

        &.before {
            justify-content: flex-start;
            left: 0;
        }

        &.after {
            justify-content: flex-end;
            right: 0;
        }
    }

    .inner {
        position: relative;
        z-index: 1;
        display: block;
        user-select: none;
        transition: transform 0.1s ease-out;
    }
}
