.buttonContainer {
    display: block;
    width: 100%;
    outline: none;

    & + .buttonContainer {
        margin-top: 10px;
    }
}

.button {
    display: flex;
    width: 100%;
    align-items: center;
    height: 44px;
    border-radius: 10px;
    background-color: #eee;
    font-weight: 500;
    font-size: 16px;
    color: #fff;
}

.icon {
    display: flex;
    width: 46px;
    height: 100%;
    justify-content: center;
    align-items: center;
}

.label {
    display: block;
    width: calc(100% - 46px);
    padding: 0 10px 0 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: left;
}
