.root {
    display: flex;
    align-items: center;
    width: 100%;

    .backLink {
        display: block;
        height: 24px;
        flex-shrink: 0;
    }

    .title {
        font-size: 28px;
        line-height: 1.25;
        margin: 0 0 0 16px;
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        flex: 1;
        white-space: nowrap;
    }

    &.slim {
        .title {
            font-size: 16px;
            line-height: 32px;
        }
    }
}
