@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.headerButtons {
    display: flex;
    align-items: center;
}

.navItem {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    background-color: #fff;
    //padding: 0 10px 0 16px;
    font-weight: normal;
    font-size: 16px;
    color: #000;

    .navItemLabel {
        @include crop();
        flex: 1;
        display: flex;
        padding: 0 10px 0 0;
        font-size: 16px;
        font-weight: bold;
        align-items: center;

        svg {
            margin-right: 16px;
        }
    }

    .navItemMeta {
        display: flex;
    }

    .navItemMetaText {
        display: flex;
        align-items: center;
        font-size: 14px;
        color: #8e8e8e;
    }

    .navItemIcon {
        margin: 0 0 0 6px;
        color: #8e8e8e;
    }
}

.storeFrontHeader {
    display: flex;
    flex-wrap: wrap;
    flex-shrink: 0;
    justify-content: space-between;
    align-items: center;
    padding: 8px 16px;
    margin: 8px 0 0 0;
    color: rgba(0, 0, 0, 0.5);

    .storeFrontHeaderHeading {
        display: block;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
    }

    .storeFrontHeaderCounter {
        display: block;
        font-size: 14px;
        line-height: 16px;
    }
}
