.root {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.choice {
    $color: #2b303a;
    display: block;
    padding: 10px 20px;
    cursor: pointer;
    border: 1px solid #dedede;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    text-decoration: none;
    color: $color;
    margin-right: 10px;

    &:last-child {
        margin-right: 0;
    }

    &:hover,
    &:focus {
        border-color: #51b3f7;
        color: $color;
    }

    &.active {
        border-color: transparent;
        color: #fff;
        background-color: #51b3f7;
    }
}
