@import '@mssgme/scss-basics/vars';

.emptyMessage {
    padding: 16px;
    text-align: center;
    margin: auto;

    > h2 {
        font-weight: bold;
        font-size: 24px;
        line-height: 1.4;
        margin: 0 0 10px 0;
    }

    > p {
        font-size: 16px;
        line-height: 1.5;
        color: #777;
        margin: 0 0 10px 0;
    }

    > div {
        margin-top: 16px;
    }
}
