@import '@mssgme/scss-basics/vars';

.tags {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    column-gap: 8px;
    row-gap: 8px;
    font-size: 80%;
}

.tag {
    background: $background-light;
    border-radius: 8px;
    padding: 2px 8px;
}

.interactive .tag {
    cursor: pointer;
}
