@import '@mssgme/scss-basics/vars';

.root {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.help {
    color: #95979c;
    font-size: 12px;
    line-height: 20px;
    text-align: center;
}

.preview {
    margin-right: 20px;

    @media all and (max-width: $mobile-breakpoint) {
        margin-right: 10px;
    }
}

.closeBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background-color: #999;
    cursor: pointer;
    position: absolute;
    top: 0;
    left: calc(50% + 50px);
    transform: translate(-11px, -11px);
    z-index: 2;

    &.disabled {
        opacity: 0.2;
        pointer-events: none;
    }
}
