.root {
    display: flex;
    width: 40px;
    height: 40px;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    color: #333;
    overflow: hidden;
    margin: 0 0 0 4px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.burger {
    width: 20px;
    height: 16px;
    position: relative;
}

.menuLine {
    width: 100%;
    height: 2px;
    border-radius: 1px;
    background-color: #333;
    position: absolute;
    left: 0;
    transition: width 0.3s ease;

    &:first-of-type {
        top: 0;
    }

    &:nth-of-type(2) {
        top: calc(50% - 1px);
    }

    &:last-of-type {
        bottom: 0;
    }
}

