.root {
    width: 100%;
    overflow: hidden;
}

.controls {
    display: flex;
    border-bottom: 1px solid #f2f2f2;
    position: relative;
}

.control {
    display: flex;
    flex: 1;
    padding: 10px 8px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    cursor: pointer;
    transition: color 0.3s ease;

    &.active {
        font-weight: 500;
        color: #00adff;
    }
}

.activeUnderLine {
    height: 2px;
    background-color: #00adff;
    position: absolute;
    bottom: -1px;
    transition: left 0.3s ease;
}

.tabsContainer {
    display: flex;
    transition: transform 0.5s ease;
}
