.root,
.overlay,
.overlay > a {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    cursor: default;
    z-index: 1;
}

.card {
    position: absolute;
    bottom: 90px;
    left: 16px;
    width: calc(100% - 32px);
    padding: 16px;
    border-radius: 10px;
    background-color: #fff;
    z-index: 1;

    &.top {
        top: 80px;
        bottom: initial;

        &::after {
            content: '';
            display: block;
            width: 50px;
            height: 50px;
            background-color: #fff;
            pointer-events: none;
            position: absolute;
            top: 0;
            left: calc(50% - 25px);
            transform-origin: 50% 50%;
            transform: rotate(45deg);
        }
    }

    &::after {
        content: '';
        display: block;
        width: 50px;
        height: 50px;
        background-color: #fff;
        pointer-events: none;
        position: absolute;
        bottom: 0;
        left: calc(50% - 25px);
        transform-origin: 50% 50%;
        transform: rotate(45deg);
    }
}

.list {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
    z-index: 1;

    > li {
        display: flex;
        align-items: center;
        margin: 0 0 16px 0;

        &:last-child {
            margin: 0;
        }
    }
}

.icon {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    overflow: hidden;
    background-color: #f7f7fa;
    width: 40px;
    height: 40px;
    flex-shrink: 0;
    color: #333;
}

.itemDescription {
    padding: 0 0 0 16px;
    font-size: 14px;
    line-height: 1.4;
}
