@import '@mssgme/scss-basics/vars';

.simpleContainer {
    position: relative;
    width: 100%;
    padding-top: 100%;
}

.thumbnail {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 10px;
    border-radius: 8px;
    overflow: hidden;
}

.text {
    font-size: 14px;
    font-weight: 600;
    line-height: 16px;
    margin-bottom: 12px;
}

.button {
    width: 65px;
    max-width: 100%;
    height: 15px;
    border-radius: 4px;
}

.badge {
    position: absolute;
    top: 4px;
    right: 4px;
    padding: 2px 5px;
    border-radius: 5px;
    font-size: 10px;
    font-weight: 500;
    pointer-events: none;
}

.swiper {
    overflow: visible;
}

.preview {
    width: 100%;
}

.bounds {
    width: 100%;
    height: 100%;
    border-radius: 8px;
    box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.08);
    position: relative;
}

.previewContent {
    width: 375px;
    padding: 0;
    border-radius: 0;
    pointer-events: none;
}

.caption {
    text-align: center;
    font-weight: bold;
}

.label {
    text-align: center;
    font-size: 12px;
    line-height: 14px;
    margin-top: 13px;
}

.pill {
    width: 100%;
    margin: 0 4px 8px 4px;
    flex: 1;
    align-items: stretch;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    &.activePill {
        .bounds {
            border: 3px solid #007aff;
        }

        .label {
            font-weight: 700;
        }
    }
}

.container {
    width: 100%;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 16px;
    row-gap: 16px;
    column-gap: 16px;

    .option {
        display: block;
        width: calc(100% / 3 - 17px);
        color: #333;
        border: none;

        &.active {
            color: #333;
            background: transparent;
            border: none;
        }

        //@media all and (min-width: $tablet-portrait-breakpoint) {
        //    width: calc(100% / 4 - 18px);
        //}
    }

    .pill .bounds {
        display: block;
    }

    .pill {
        display: block;
        margin: 0;
    }

    .activePill .bounds {
        border: none;

        &::after {
            content: '';
            display: block;
            width: calc(100% + 8px);
            height: calc(100% + 8px);
            border-radius: 11px;
            border: 2px solid #2979ff;
            position: absolute;
            top: -4px;
            left: -4px;
            box-sizing: border-box;
        }
    }
}

.radio {
    .pill {
        margin-bottom: 0;
    }

    .label {
        display: none;
    }

    .thumbnail,
    .previewContent,
    .bounds,
    .button {
        border-radius: 50%;
    }

    .bounds {
        margin-top: 2px;
        padding: 2px;
        border: none;
    }

    .activePill .bounds {
        border: none;

        &::after {
            content: '';
            display: block;
            width: calc(100% + 4px);
            height: calc(100% + 4px);
            border-radius: 50%;
            border: 2px solid #2979ff;
            position: absolute;
            top: -2px;
            left: -2px;
            box-sizing: border-box;
        }
    }

    .thumbnail {
        padding: 25%;

        .thumbnailContent {
            display: block;
            width: 100%;
            height: 100%;
        }
    }

    .text {
        display: none;
    }

    .button {
        border-radius: 50%;
        width: 100%;
        height: 100%;
    }

    .badge {
        top: unset;
        right: -2px;
        bottom: -2px;
        font-size: 6px;
        line-height: 6px;
        min-height: unset;
        padding: 2px;
        z-index: 100;
    }

    .preview {
        border-radius: 50%;
    }
}

.pagePreview {
    $width: 375px;
    $height: 500px;
    position: relative;
    margin: 0 auto 16px auto;
    width: $width * 0.5;
    height: $height * 0.5;

    & > div {
        width: $width;
        height: $height;
        min-width: $width;
        min-height: $height;
        border-radius: 16px;
        transform: scale(0.5);
        transform-origin: 0 0;
        pointer-events: none;
        user-select: none;
    }
}
