.disabled {
    opacity: 0.6;
    pointer-events: none;
}

.row {
    display: grid;
    grid-template-rows: auto auto 1fr;
    grid-template-columns: 1fr;
    height: 100%;
}

.separator {
    width: 100%;
    height: 8px;
    background-color: #f7f7fa;
}

.list {
    min-height: 0;
    background: #fff;
}

.labelText {
    flex: 1;
    margin-left: 16px;
}

.labelButton {
    margin-right: 16px;;
}
