.root {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 58px;
    background-color: #dc5960;
}

.link {
    display: flex;
    padding: 0 24px;
    align-items: center;
    height: 100%;
    width: 100%;
    overflow: hidden;
    font-size: 14px;
    line-height: 18px;
    color: #fff;
}

.message {
    flex-grow: 1;
}

.button {
    display: block;
    border-radius: 100px;
    background-color: #fff;
    padding: 6px 8px;
    font-size: 14px;
    line-height: 16px;
    color: #dc5960;
    flex-grow: 0;
    flex-shrink: 0;
}
