@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    width: 100%;
    flex-direction: column;
    min-height: 100%;
    position: relative;
    padding: 0 0 16px 0;
    background-color: #fff;
}

.pageHeader {
    padding: 24px 16px 57px 16px;
    background-color: #f7f7fa;
    display: flex;
    align-items: center;
    width: 100%;

    .backLink {
        display: block;
        height: 24px;
        flex-shrink: 0;
    }

    .title {
        font-size: 34px;
        line-height: 1;
        margin: 0 0 0 16px;
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        flex: 1;
        white-space: nowrap;
    }
}

.pagesContainer {
    display: flex;
    width: 100%;
    flex-direction: column;
    padding: 0 16px 0 16px;
    position: relative;
    margin: -41px 0 0 0;
    flex: 1;
    align-items: center;
}

.preview {
    width: 60px;
    min-width: 60px;
    min-height: 81px;
    margin: 0 16px 0 -5px;
}

.multipleGroups {
    .pageHeader {
        padding: 24px 16px 16px 16px;
    }

    .pagesContainer {
        margin: 0;
    }
}
