@import '@mssgme/scss-basics/vars';

.root {    
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    transition: padding $default-transition;

    @media all and (max-width: 600px) {
        padding: 0;
    }
}

.mainContainer {
    position: relative;
    margin: auto;
    width: 455px;
    max-width: 100%;
    height: 820px;
    max-height: 100%;
    overflow: hidden;
    -webkit-overflow-scrolling: touch;
    transform: translate3d(0, 0, 0);
    transition: filter 0.6s ease, width $default-transition, height $default-transition, border-radius $default-transition;
    border-radius: 16px;
    box-shadow: 0 2px 30px 0 rgba(0, 0, 0, 0.08);

    @media all and (max-width: 600px) {
        width: 100%;
        height: 100%;
        box-shadow: none;
        border-radius: 0;
    }
}

.mainContainerInner {
    position: absolute;
    top: var(--m-container-top);
    left: 0;
    width: 100%;
    height: var(--m-container-height);
    overflow: auto;

    &.hasHeader {
        --m-container-top: var(--top-banner-height);
        --m-container-height: calc(100% - var(--m-container-top));
    }
}

.mainContainerInner::-webkit-scrollbar {
    width: 0;
}

.mainContainerInner::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0);
}

.mainContainerInner::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0);
}
