.root {
    display: flex;
    align-items: center;
}

.label {
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
    flex-shrink: 0;
    height: 28px;
    min-width: 50px;
    padding: 5px 10px;
    border-radius: 8px;
    background-color: #f5f5f5;
    margin: 4px 10px 0 0;
    font-size: 14px;
    line-height: 1;
    color: #777;

    &.right {
        width: 70px;
        margin: 4px 0 0 10px;
        font-weight: 500;
        color: #2979ff;
    }
}

.inputWrapper {
    flex: 1;

    input[type='range'] {
        -webkit-appearance: none;
        margin: 10px 0;
        width: 100%;
    }

    input[type='range']:focus {
        outline: none;
    }

    input[type='range']::-webkit-slider-runnable-track {
        width: 100%;
        height: 4px;
        cursor: pointer;
        box-shadow: 0 0 0 #000000, 0 0 0 #0d0d0d;
        background: #ccc;
        border-radius: 100px;
        border: 0 solid #000101;
    }

    input[type='range']::-webkit-slider-thumb {
        box-shadow: 0 0 6px rgba(0, 0, 0, 0.3), 0 0 0 #0d0d0d;
        border: 0 solid #000000;
        height: 24px;
        width: 24px;
        border-radius: 50%;
        background: #fff;
        cursor: pointer;
        -webkit-appearance: none;
        margin-top: -10px;
    }

    input[type='range']:focus::-webkit-slider-runnable-track {
        background: #ccc;
    }

    input[type='range']::-moz-range-track {
        width: 100%;
        height: 4px;
        cursor: pointer;
        animation: 0.2s;
        box-shadow: 0 0 0 #000000, 0 0 0 #0d0d0d;
        background: #ccc;
        border-radius: 25px;
        border: 0 solid #000101;
    }

    input[type='range']::-moz-range-thumb {
        box-shadow: 0 0 6px rgba(0, 0, 0, 0.3), 0 0 0 #0d0d0d;
        border: 0 solid #000000;
        height: 24px;
        width: 24px;
        border-radius: 50%;
        background: #fff;
        cursor: pointer;
    }

    input[type='range']::-ms-track {
        width: 100%;
        height: 4px;
        cursor: pointer;
        animation: 0.2s;
        background: transparent;
        border-color: transparent;
        border-width: 39px 0;
        color: transparent;
    }

    input[type='range']::-ms-fill-lower {
        background: #ccc;
        border: 0 solid #000101;
        border-radius: 50px;
        box-shadow: 0 0 0 #000000, 0 0 0 #0d0d0d;
    }

    input[type='range']::-ms-fill-upper {
        background: #ccc;
        border: 0 solid #000101;
        border-radius: 50px;
        box-shadow: 0 0 0 #000000, 0 0 0 #0d0d0d;
    }

    input[type='range']::-ms-thumb {
        box-shadow: 0 0 6px rgba(0, 0, 0, 0.3), 0 0 0 #0d0d0d;
        border: 0 solid #000000;
        height: 24px;
        width: 24px;
        border-radius: 50%;
        background: #fff;
        cursor: pointer;
    }

    input[type='range']:focus::-ms-fill-lower {
        background: #ccc;
    }

    input[type='range']:focus::-ms-fill-upper {
        background: #ccc;
    }
}

.disabled {
    .label {
        color: rgba(0, 0, 0, 0.2);
    }

    .inputWrapper {
        opacity: 0.4;
    }
}
