@import '@mssgme/scss-basics/mixins/scrollbar';

.toolbar {
    display: flex;
    flex-shrink: 0;
    background: #fff;
    padding: 4px;
    flex-wrap: wrap;
    align-items: center;
    overflow: hidden;

    &.small {
        padding: 3px;
    }

    &.nowrap {
        flex-wrap: nowrap;
        overflow: auto;
        @include scrollbar();
    }
}
