@import '@mssgme/scss-basics/mixins/scrollbar';
@import '@mssgme/scss-basics/mixins/crop';

.selector {
    position: relative;
    overflow: auto;
    @include scrollbar();
}

.saveButton {
    padding-right: 16px;
    height: 100%;
    font-size: 16px;
}

.modalTitle {
    pointer-events: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 100%;
    overflow: hidden;
    padding: 0 10px;
    cursor: pointer;
}

.chevronIcon {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 0 0 6px;
    flex-shrink: 0;
}

.pageName {
    flex-shrink: 1;
    @include crop();
}
