.root {
    margin: -16px 0 0 0;

    .intro {
        text-align: center;
        margin: 0 0 28px 0;

        .title {
            font-weight: 700;
            font-size: 34px;
            margin: 0;
        }

        .subHeading {
            margin: 10px 0 0 0;
        }

        .link {
            font-weight: 700;
            margin: 2px 0 0 0;
        }
    }

    .list {
        margin: 0;
        padding: 0 8px;
        list-style: none;
        font-weight: 500;
        font-size: 18px;

        > li {
            border-bottom: 2px solid #f0f0f0;

            &:last-of-type {
                border: none;
            }

            > a,
            > div {
                display: block;
                position: relative;
                padding: 12px 36px 12px 0;
                color: #333;
                cursor: pointer;
                user-select: none;
                overflow: hidden;

                .icon {
                    display: flex;
                    width: 26px;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    right: 0;
                    justify-content: center;
                    align-items: center;
                    transition: transform 0.3s ease, opacity 0.3s ease;
                }

                &.copyLink {
                    > span {
                        display: block;
                        transition: transform 0.3s ease, opacity 0.3s ease;
                    }

                    .notificationText {
                        pointer-events: none;
                        position: absolute;
                        top: 12px;
                        left: 0;
                        padding: 0 36px 0 0;
                        opacity: 0;
                        transform: translateY(40px);
                    }

                    .icon.hidden {
                        opacity: 0;
                        transform: translateY(40px);
                    }

                    &.copied {
                        color: #0aa89a;

                        .primaryText,
                        .icon {
                            transform: translateY(-40px);
                            opacity: 0;
                        }

                        .icon.hidden,
                        .notificationText {
                            opacity: 1;
                            transform: none;
                        }
                    }
                }
            }
        }
    }
}
