@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    flex-direction: column;

    .list {
        margin: 0;
        padding: 0;
        overflow: auto;
    }
}

.field {
    margin-bottom: 6px;
}

.row {
    width: 100%;
    display: flex;
    align-items: center;
    border-radius: 8px;
    background-color: #f7f7fa;
    padding: 0 6px 0 16px;
    height: 46px;
    cursor: pointer;
    user-select: none;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.5;
    position: relative;
    overflow: hidden;

    .layer {
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }

    .left {
        display: flex;
        align-items: center;
        flex-shrink: 0;
        position: relative;
        z-index: 1;
        height: 100%;

        .iconWrapper {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 50px;
            height: 100%;
            margin: 0 16px 0 -16px;
            background: #ededf0;

            .icon {
                flex-shrink: 0;
                width: 20px;
                height: 20px;
            }
        }
    }

    .value {
        width: 100%;
        padding: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-weight: bold;

        &.active {
            color: #333;
        }
    }

    .buttons {
        display: flex;
        height: 100%;
        padding: 6px 0;
        flex-shrink: 0;
        align-items: center;
        position: relative;
        z-index: 1;

        .changeButton {
            display: block;
            padding: 2px 8px;
            border-radius: 6px;
        }

        .button {
            display: flex;
            height: 100%;
            justify-content: center;
            align-items: center;
            overflow: hidden;
            position: relative;
            border-radius: 6px;
            font-weight: 500;
            font-size: 14px;
            line-height: 14px;
            color: #2979ff;
            cursor: pointer;
            transition: color 0.3s ease;
            min-width: 32px;

            &.icon {
                padding: 0;
            }

            &.remove {
                color: #7c7c7d;

                &:hover {
                    color: #333;
                }
            }

            &.danger {
                color: $danger;
            }
        }
    }

    &.small {
        height: 38px;
        border-radius: 10px;

        .left {
            .iconWrapper {
                width: 38px;

                .icon {
                    width: 18px;
                    height: 18px;
                }
            }
        }

        .value {
            font-weight: 600;
            flex-shrink: 0;
            width: auto;
            margin-right: auto;
        }

        .previewValue {
            width: auto;
            overflow: hidden;
            text-overflow: ellipsis;
            padding: 0 0 0 20px;
            font-size: 14px;
            color: #999;
            white-space: nowrap;
        }
    }
}
