@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    width: 100%;
    height: var(--canvas-header-height);
    background-color: $background-light;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    transition: background-color $default-transition, top $default-transition, box-shadow $default-transition;
    overflow: hidden;

    &.viewMode {
        top: calc(-1 * var(--canvas-header-height));
        box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    }

    .container {
        width: 100%;
        height: 200%;
        transition: transform 0.3s ease, height 0.3s ease;
    }

    &.sortMode { // TODO: remove
        background-color: #fff;

        .container {
            //height: 0;
            transform: translateY(-50%);
        }
    }
}

.row {
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.backButton {
    width: var(--canvas-header-height);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.button {
    width: var(--canvas-header-height);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: transform 0.3s ease, opacity 0.3s ease;
    flex-shrink: 0;
}

.title {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex: 1 1 0;
    position: relative;
    overflow: hidden;
}

.edit {
    position: relative;
    pointer-events: none;
    flex: 0 0 32px;
}

.title .input {
    padding: 0 !important;
    border: none;
    flex: 1;
    display: flex;
    align-items: center;
}

.crop {
    @include crop();
}

.publishButton {
    user-select: none;
    margin-right: 10px;
}
