.fieldTip {
    white-space: pre-wrap;
    //color: #333;
}

.list {
    margin: 0 0 10px 0;
    padding: 0 0 0 16px;

    & > li {
        margin: 0 0 6px 0;

        &:last-of-type {
            margin: 0;
        }

        b {
            font-weight: bold;
            color: #555;
        }
    }
}

.statusContainer {
    margin: 16px 0 0 0;

    .label {
        display: block;
        padding: 0 10px;
        margin: 0 0 4px 0;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
    }

    .status {
        display: flex;
        width: 100%;
        min-height: 38px;
        justify-content: space-between;
        align-items: center;
        padding: 0 16px;
        background-color: #eee;
        border-radius: 10px;

        .domain {
            flex: 1;
            flex-shrink: 0;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;

            > a,
            > span {
                flex: 1;
                flex-shrink: 0;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                display: block;
                font-weight: normal;
            }
        }
    }
}
