.root {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    align-items: center;
    margin: 0;
    column-gap: 2px;

    &:before {
        content: '';
        width: 1px;
        height: 22px;
        background-color: #eee;
        margin: 0 4px;
    }

    &:first-child:before {
        display: none;
    }
}
