.root {
    display: flex;
    justify-content: center;
    align-items: center;
    height: auto;
    min-width: 26px;
    min-height: 26px;
    border-radius: 6px;
    transition: all 0.3s ease;

    &.simple {
        min-width: auto;
        min-height: auto;
    }
}

.transparent {
    color: #7c7c7d;
    background-color: transparent;

    &:hover {
        color: #333;
    }
}

.primary {
    $bg-color: #007aff;
    color: #fff;
    background-color: $bg-color;

    &:hover {
        background-color: darken($bg-color, 10%);
    }
}

.primary2 {
    $bg-color: #ff3265;
    color: #fff;
    background-color: $bg-color;
}

.secondary {
    $bg-color: #f9f9f9;
    color: #29323c;
    background-color: $bg-color;

    &:hover {
        background-color: darken($bg-color, 10%);
    }
}

.danger {
    $bg-color: #dc5960;
    $color: #fff;
    color: $color;
    background-color: $bg-color;

    &:hover {
        background-color: darken($bg-color, 20%);
    }

    &:active {
        transition: none;
        color: rgba(255, 255, 255, 0.6);
    }
}
