@import '@mssgme/scss-basics/mixins/crop';

.root {
    border-radius: 10px;
    background-color: #f7f7fa;
    position: relative;
    margin: 0 0 14px 0;

    &:last-of-type {
        margin: 0;
    }
}

.total {
    display: flex;
    justify-content: space-between;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    position: relative;
    padding: 10px 16px;
    cursor: pointer;
}

.chevron {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 42px;
    position: absolute;
    top: 0;
    right: 0;
    transition: transform 0.3s ease;
}

.hasChevron {
    padding-right: 36px;
    cursor: pointer;
}

.disabled {
    pointer-events: none;

    .chevron {
        opacity: 0.2;
    }
}

.expanded {
    transform: rotate(180deg);
}

.delta {
    font-size: 80%;
    line-height: 18px;

    .up {
        color: green;
    }

    .down {
        color: red;
    }
}

.button {
    display: block;
    position: absolute;
    bottom: 10px;
    right: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #007aff;
    cursor: pointer;
    padding: 0 12px 0 0;

    &::after {
        content: '';
        display: block;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 5px 3px 0 3px;
        border-color: #007bff transparent transparent transparent;
        position: absolute;
        right: 0;
        top: 7px;
    }

    &.expanded::after {
        transform: rotate(180deg);
    }
}

.list {
    font-size: 14px;
    line-height: 20px;
    padding: 0 36px 0 16px;
    list-style: none;
    overflow: hidden;
    margin: 0;
    transition: padding 0.3s ease;

    > li {
        display: flex;
        justify-content: space-between;
        padding: 11px 0 8px 0;

        &:last-of-type {
            margin: 0 0 8px 0;
        }

        > span:first-of-type {
            display: block;
            flex: 1;
            @include crop();
            padding: 0 10px 0 0;
        }
    }
}
