.root {
}

.code {
    padding: 12px 16px;
    border-radius: 10px;
    background-color: #f5f5f5;
    white-space: pre-wrap;
    word-break: break-all;
    font-size: 14px;
    user-select: text;
    margin: 0 0 16px 0;
}

.label {
    display: flex;
    width: 100%;
    color: #333;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 4px;
    padding-left: 10px;
}
