@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    height: 100vh;
    overflow: hidden;
    position: relative;
    padding: 20px;
}

.content {
    width: 100%;
    height: 200px;
    max-width: 335px;
    margin: 0 auto;
}

.heading {
    color: #1b1e24;
    font-size: 32px;
    font-weight: bold;
    line-height: 42px;
    margin-top: 0;
    margin-bottom: 10px;
}

.message {
    color: #777;
    font-size: 16px;
    line-height: 24px;
    margin: 0 0 15px 0;
}

.footer {
    position: absolute;
    top: 20px;
    left: 30px;

    @media all and (max-width: $mobile-breakpoint) {
        left: 10px;
    }
}

.buttonsContainer {
    margin: 0;

    > span {
        display: inline-block;
        vertical-align: baseline;
        font-size: 16px;
        color: #777;
        margin: 0 10px;
    }

    > a {
        @media all and (max-width: $mobile-breakpoint) {
            padding: 0 15px;
        }
    }
}
