.p {
    font-size: 16px;
    line-height: 22px;
    margin: 16px 0 8px 0;
    text-align: center;

    b {
        font-weight: 600;
    }
}

.logo {
    margin: 0 0 16px 0;

    > svg {
        display: block;
        width: 41px;
        height: auto;
        margin: 0 auto;
    }
}

.heading {
    margin: 0 0 16px 0;

    > h1 {
        font-weight: 700;
        font-size: 34px;
        line-height: 40px;
        margin: 0;
    }

    > p {
        font-size: 16px;
        line-height: 22px;
        margin: 7px 0 0 0;

        b {
            font-weight: 600;
        }
    }
}

.blocksList {
    list-style: none;
    margin: 0;
    padding: 0;

    > li {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: 0 0 8px 0;
        background-color: #f5f5f5;
        border-radius: 10px;
        padding: 6px 12px;

        &:last-of-type {
            margin: 0;
        }

        > span {
            display: block;

            &:first-child {
                font-weight: 500;
            }
        }
    }
}
