@import '@mssgme/scss-basics/vars';

.root {
    overflow: hidden;
    min-height: 100%;
}

.bg {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
}

.blockWrapper {
    cursor: pointer;
    pointer-events: auto;
    position: relative;
}

.block {
    outline: 3px solid transparent;
    outline-offset: 3px;
    border-radius: 4px;
    transition: outline $default-transition;
    cursor: pointer;
    position: relative;
    pointer-events: none;
}

.active {
    outline: 3px solid $primary;

    &::after {
        content: '';
        display: block;
        width: calc(100% + 6px);
        height: calc(100% + 6px);
        position: absolute;
        top: -3px;
        left: -3px;
        background-color: rgba(41, 121, 255,0.4);
    }
}
