// TODO: refactor, don't use nested
.planPane {
    width: 100%;
    border-radius: 8px;
    background-color: #fff;
    padding: 16px;
    box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.12);
}

.planPaneHeader {
    margin: 0 0 16px 0;

    h2 {
        width: 100%;
        font-weight: 700;
        font-size: 24px;
        line-height: 28px;
        margin: 0 0 8px 0;
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        > span {
            display: block;

            &.planName {
                margin-right: auto;
            }

            &.currentPlanLabel {
                font-weight: 500;
                font-size: 12px;
                color: #777;
                border-radius: 6px;
                background-color: #f0f0f0;
                padding: 0 12px;
            }
        }
    }

    p {
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        margin: 0;

        span {
            font-weight: 700;
            font-size: 26px;
            line-height: 30px;
        }
    }
}

.planPaneFeaturesList {
    list-style: none;
    margin: 16px 0 0 0;
    padding: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;

    > li {
        margin: 0 0 6px 0;
        padding: 0 0 0 24px;
        position: relative;

        &:last-of-type {
            margin: 0;
        }

        .checkmark {
            width: 24px;
            height: 24px;
            display: flex;
            align-items: center;
            color: #2979FF;
            position: absolute;
            top: 0;
            left: 0;

            svg {
                display: block;
            }
        }
    }
}
