@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

$thumbSize: 70px;
$padding: 16px;

.root {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    color: #000;
    position: relative;
    background-color: #fff;
    padding: 0 $padding;

    &.disabled {
        opacity: 0.4;
        filter: grayscale(1);
    }

    &.noPadding {
        padding: 0;
    }
}

.productRoot {
    flex: 1;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: stretch;
    overflow: hidden;

    @media all and (min-width: $tablet-landscape-breakpoint) {
        &:hover {
            .title {
                color: $link-color;
            }
        }
    }
}

.thumbnail {
    background-color: #efefef;
    overflow: hidden;
    position: relative;
    width: $thumbSize;
    height: $thumbSize;
    flex-basis: $thumbSize;
    min-width: $thumbSize;
    box-shadow: none;
    border-radius: 8px;
    margin: $padding 0;

    img {
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
    }
}

.emptyPlaceholder {
    width: 100%;
    height: 100%;
    background-color: #eee;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 10px;
}

.meta {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: $padding 0;
    margin-left: $padding;
    overflow: hidden;
}

.title {
    display: block;
    font-weight: bold;
    font-size: 14px;
    line-height: 18px;
    color: #000;
    margin: 0;
    width: 100%;
    transition: color 0.3s ease;
    @include crop();
}

.price {
    @include crop();
    display: block;
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    color: #999;
    margin: 8px 0 0 0;
}

.discountPrice {
    font-weight: normal;
    color: #000;
    margin: 0 8px 0 0;
}

.lineThrough {
    text-decoration: line-through;
}

.status {
    font-weight: lighter;
    font-size: 12px;
    line-height: 18px;

    &.published {
        color: #43a047;
    }
}

.root + .root {
    .meta {
        border-top: 1px solid $thin-line-color;
    }
}

.notes {
    display: block;
    font-size: 12px;
    font-weight: normal;
    line-height: 14px;
    color: #666;
    margin-left: 16px;
    flex: 1;
    white-space: pre-wrap;
    text-align: right;
    @include cropLines(3);
}

.columns {
    display: flex;
    flex-direction: row;
}
