@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/scrollbar';

[contenteditable] {
    -webkit-user-select: text;
    user-select: text;
}

.root {
    position: relative;
    min-height: 0;
    display: flex;
    flex: 1;
    flex-direction: column;
}

.editorInput,
.editorPlaceholder {
    flex: 1;
    padding: 16px;
    outline: 0;
    min-height: 150px;
    max-height: 250px;
    overflow: auto;
    @include scrollbar();

    *::selection {
        background: var(--selection-bg-color);
        color: var(--selection-color);
    }
}

.editorPlaceholder {
    background: none;
    position: absolute;
    pointer-events: none;
    top: 0;
    width: 100%;
    opacity: 0.5;
}
