@import '@mssgme/scss-basics/vars';

.root {
    background: $background-light;
    display: grid;
    grid-template-rows: 1fr auto;
    grid-template-columns: 1fr;
    height: 100%;
}

.filter {
    background: #fff;
    padding: 16px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    border-top: 8px solid #f7f7fa;
}

.static {
    position: static;
    opacity: 0;
    pointer-events: none;
}

.list {
    padding: 0;
    min-height: 0;
}

.saveButton {
    padding-right: 16px;
    height: 100%;
    font-size: 16px;
}
