@import '@mssgme/scss-basics/vars';

$gutterWidth: 40px;

.root {
    position: relative;
}

.dragHandle {
    pointer-events: auto !important;
    width: $gutterWidth !important;
    position: absolute;
    right: 0;
    color: $placeholder-light-color !important;
    transition: opacity $x-quick-transition !important;
}

.checkMark {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: $gutterWidth;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity $x-quick-transition;
    color: $placeholder-light-color;
    pointer-events: none;

    > .checkIcon {
        transition: border-color $quick-transition;
        border: 2px solid $placeholder-light-color;
        border-radius: 50%;
        width: 12px;
        height: 12px;
    }

    @media (hover: hover) and (pointer: fine) {
        &:hover > .checkIcon {
            border-color: $link-color3;
        }
    }

    &.checked > .checkIcon {
        border-color: $link-color3;
    }
}

.hiddenWrapper {
    margin: 0;
}

.content {
    max-width: 100%;
    overflow: visible;

    transform-origin: 50% 0;
    opacity: 0;

    &.ready {
        opacity: 1;
    }

    &.animated {
        transition: transform $scale-transition, margin $scale-transition, opacity $scale-transition;
        will-change: transform, margin, opacity;
    }
}

.wrapper {
    cursor: pointer;
    user-select: none;
    position: relative;
    overflow: hidden;

    &.draggable {
        cursor: grab;
    }

    &.interactive {
        cursor: auto;
        user-select: auto;
    }
}

.nested {
    display: none;
}

.dragged {
}

.cropped {
    height: 0;
    opacity: 0;
    max-height: 48px;
    overflow: hidden;
    transition: height $quick-transition, opacity $default-transition;
}

.nonInteractive {
    &.hiddenWrapper {
        margin: 16px 0;
    }

    & > .contentWrapper {
        user-select: none;
    }

    .content {
        pointer-events: none;
    }

    .cropped {
        opacity: 1;
        height: 48px;
        max-height: 48px;
    }

    .checkMark {
        opacity: 1;
        pointer-events: auto;
    }
}
