@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.item {
    display: flex;
    flex-direction: row;

    & + & {
        margin-top: 8px;
    }
}

.thumbnail {
    width: 40px;
    height: 40px;
    position: relative;
    border-radius: 8px;
    overflow: hidden;
}

.meta {
    display: flex;
    flex-direction: column;
    padding-left: 16px;
    flex: 1;
}

.title {
    font-weight: 500;
    font-size: 14px;
}

.details {
    flex: 1;
    font-weight: 300;
    font-size: 12px;
    display: inline-flex;
    justify-content: space-between;
    column-gap: 16px;
}

.price {
    white-space: nowrap;
}

.notes {
    opacity: 0.6;
}
