@import '@mssgme/scss-basics/mixins/crop';
@import '@mssgme/scss-basics/mixins/scrollbar';

.root {
    display: grid;
    grid-template-rows: auto 1fr;
    overflow: hidden;
}

.selector {
    overflow: auto;
    padding: 16px;
    @include scrollbar();
}

.labelItem {
    padding: 0 10px 0 0;
}

.labelName {
    font-weight: 500;
    @include crop();
}

.labelUrl {
    font-size: 14px;
    opacity: 0.6;
    @include crop();
}
