.root {
    z-index: 100;
}

.root,
.overlay,
.overlay > a {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    cursor: default;
}

.card {
    position: absolute;
    left: 16px;
    bottom: 16px;
    transform: translateY(-50%);
    width: calc(100% - 32px);
    padding: 16px;
    border-radius: 10px;
    background-color: #fff;
}

.block {
    position: relative;
    width: 100%;
    height: 50px;
    background-color: #eee;
    border-radius: 10px;
    margin: 0 0 16px 0;

    &:last-child {
        margin: 0;
    }
}

.heading {
    display: inline-block;
    margin: 0 0 8px 0;
    font-size: 14px;
    padding: 4px 8px;
    border-radius: 8px;
    background-color: #333;
    color: #fff;
    font-weight: 500;
}

.tip {
    margin: 0 0 16px 0;
}

.handBlock {
    width: 40px;
    position: absolute;
    top: 5px;
    left: 50%;
}

.hand {
    display: block;
    width: 100%;
    height: auto;
}
