.root {
    .pitch {
        text-align: center;
    }

    .grid {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 16px;
    }

    .option {
        flex: 0 0 calc((100% - 16px) / 2);
        padding: 3px;
        cursor: pointer;
    }

    .pill {
        width: 100%;
        height: 100%;
        border-radius: 8px;
        box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.12);
        overflow: hidden;
        display: flex;

        img {
            width: 100%;
            display: block;
        }

        .innerLabel {
            display: flex;
            flex: 1;
            align-items: center;
            justify-content: center;
        }
    }
}

.preview {
    height: 100%;

    .templateView {
        pointer-events: none;
    }

    .actions {
        position: absolute;
        width: 100%;
        bottom: 0;
        display: flex;
        justify-content: space-between;
        padding: 16px;
        z-index: 1;

        .button {
            height: 56px;
            //background-color: #fff;
            box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.12);
            padding: 16px;
            border-radius: 100px;
            line-height: 24px;

            &.cancel {
                background-color: #fff;
            }
        }
    }
}
