@import '@mssgme/scss-basics/vars';

.root {
    cursor: text;

    .input {
        flex: 1;

        font-size: 16px;
        font-weight: normal;
        height: 42px;
        line-height: 24px;
        border-radius: 4px;
        width: 100%;
        padding: 9px 16px;
        color: #2b303a;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
}
