.dropdown {
    z-index: 5;
    display: block;
    position: absolute;
    box-shadow: 0 12px 28px 0 rgba(0, 0, 0, 0.2), 0 2px 4px 0 rgba(0, 0, 0, 0.1),
    inset 0 0 0 1px rgba(255, 255, 255, 0.5);
    border-radius: 8px;
    background-color: #fff;
}

.items {
    display: flex;
    flex-direction: column;
    min-width: 100px;
    min-height: 40px;
    padding: 8px 0;
    row-gap: 4px;
}

.picker {
    min-width: 100px;
    min-height: 40px;
}

.applyButton {
    margin: 0 16px 16px 16px;
    width: calc(100% - 32px);
}

.item {
    padding: 8px;
    color: #050505;
    cursor: pointer;
    line-height: 16px;
    font-size: 15px;
    display: flex;
    align-content: center;
    flex-direction: row;
    flex-shrink: 0;
    background-color: #fff;
    border-radius: 8px;
    border: 0;
    min-width: 268px;
}

.chevronDown {
    margin-top: 3px;
    width: 16px;
    height: 16px;
    display: flex;
    user-select: none;
    background-color: transparent;
    background-size: contain;
}
