.root {
    width: 100%;
    height: 100%;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 60px 16px;
    //position: absolute;
    //top: 0;
    //left: 0;
}

.card {
    text-align: center;
    position: relative;

    > h2 {
        font-weight: bold;
        font-size: 24px;
        line-height: 1.2;
        margin: 0 0 8px 0;
    }

    > p {
        font-size: 15px;
        opacity: 0.5;
        margin: 0;
    }

    > p.divider {
        display: block;
        margin: 16px 0;
    }
}

.downIconWrapper {
    position: absolute;
    left: calc(50% - 15px);
    bottom: -100px;
    opacity: 0.9;
}
