// TODO: refactor
.root {
    width: calc(100% + 32px);
    margin: 0 0 0 -16px;
    display: flex;
    flex-direction: column;
    border-top: 1px solid #f2f2f2;
    padding: 10px 0 0 0;

    .clickablePagination {
        position: unset !important;
        line-height: 30px;

        .activeBullet {
            background-color: #333 !important;
            opacity: 1 !important;
        }
    }

    .section {
        display: flex;
        flex-direction: column;
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 0;
        }

        .sectionLabel {
            font-weight: bold;
            margin-bottom: 8px;
        }

        .fields {
            flex: 1;
            margin: 0;
            width: 100%;
            z-index: 0;

            .field {
                font-size: 14px;
                text-align: center;
                cursor: pointer;
                white-space: nowrap;
                padding: 0 6px;
                user-select: none;

                .label {
                    width: 100%;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    color: #999;
                    font-size: 12px;
                    margin: 2px 0 0 0;
                }

                .preview {
                    > div {
                        max-width: 100%;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        margin: 0 auto;
                    }
                }

                .knob {
                    width: 100%;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    color: #999;
                }
            }

            .field + .field {
                margin-left: 8px;
            }
        }
    }
}

.slide {
    justify-content: center;
}

.colorField {
    width: 21px;
    height: 21px;
    border-radius: 50%;
    border: 3px solid #f2f2f2;
    background-size: 8px 8px;
    background-position: 0 0, 0 4px, 4px -4px, -4px 0;
    background-image: linear-gradient(45deg, rgba(0, 0, 0, 0.03) 25%, transparent 25%),
        linear-gradient(-45deg, rgba(0, 0, 0, 0.03) 25%, transparent 25%),
        linear-gradient(45deg, transparent 75%, rgba(0, 0, 0, 0.03) 75%),
        linear-gradient(-45deg, transparent 75%, rgba(0, 0, 0, 0.03) 75%);

    .colorFieldColor {
        width: 100%;
        height: 100%;
        border-radius: 50%;
    }
}

.iconField {
    height: 21px;
    display: flex;
    align-items: center;
    justify-content: center;

    &.iconFieldRounded {
        width: 32px;
        border-radius: 50%;
        border: 1px solid #333;
        padding: 2px;
        overflow: hidden;
    }

    svg {
        :global(.social-svg-icon) {
            fill: #333 !important;
        }
    }
}

.badge {
    bottom: 0;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 10px;
    line-height: 6px;
    padding: 0 4px;
    min-height: 16px;
    margin: 2px 0 3px 0;
}

:global {
    .swiperPaginationBullet {
        cursor: pointer;
    }
}
