.qr {
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: scale(0.8);
    opacity: 0;
    animation: revealQr 0.6s ease forwards;
}

.qrWrapper {
    width: 100%;
    padding-top: 100%;
    position: relative;
}

.spinnerContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.tip {
    margin: 0;
    text-align: center;
    animation: revealOpacity 0.6s ease forwards;
}

.button {
    opacity: 0;
    animation: revealOpacity 0.6s ease forwards;
}

@keyframes revealQr {
    0% {
        transform: scale(0.8);
        opacity: 0;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes revealOpacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
