.root {
    display: flex;
    width: 100%;
    height: 88px;
    padding: 16px;
    justify-content: space-between;
    align-items: center;
}

.logo {
    display: block;
    height: 24px;
    cursor: pointer;
    color: #333;

    > svg {
        display: block;
        height: 100%;
        width: auto;
    }
}

.headerRight {
    display: flex;
    align-items: center;
    margin: 0 -6px 0 0;
}

.name {
    display: block;
    padding: 0 10px 0 0;
    font-weight: normal;
    font-size: 14px;
    color: #777;
    max-width: 180px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.help {
    width: 34px;
    height: 34px;
}
