// TODO: refactor, don't use nested
.contentContainer {
}

.logo {
    margin: -35px 0 26px 0;

    > svg {
        display: block;
        width: 41px;
        height: auto;
        margin: 0 auto;
    }
}

.heading {
    margin: -20px 0 24px 0;
    text-align: center;

    > h1 {
        font-weight: 700;
        font-size: 34px;
        line-height: 40px;
        margin: 0;
    }

    > p {
        font-size: 16px;
        line-height: 22px;
        margin: 7px 0 0 0;
    }
}

.plansList {
    margin: 0;
    padding: 0;
    list-style: none;

    > li {
        position: relative;
        min-height: 52px;
        border-radius: 10px;
        background-color: #F7F7FA;
        display: flex;
        align-items: center;
        margin: 0 0 8px 0;
        padding: 11px 15px;
        font-size: 14px;
        line-height: 1;
        cursor: pointer;
        border: 1px solid #F7F7FA;
        user-select: none;
        transition: border-color .3s ease, background-color .3s ease;

        &:last-of-type {
            margin: 0;
        }

        &.active {
            background-color: rgba(41, 121, 255, 0.05);
            border: 1px solid #2979FF;
        }

        &.disabled {
            pointer-events: none;
        }

        .plansListTextLine {
            margin: 0 0 2px 0;

            &:last-of-type {
                margin: 0;
            }

            > span {
                display: inline-block;
                font-weight: 600;
                font-size: 16px;
                color: #000;
                line-height: 28px;
            }
        }

        .label {
            display: block;
            position: absolute;
            top: 12px;
            right: 12px;
            border-radius: 6px;
            background-color: #2979ff;
            padding: 2px 8px;
            font-weight: 600;
            font-size: 12px;
            line-height: 24px;
            color: #fff;

            &.currentPeriod {
                background-color: #f0f0f0;
                font-weight: 500;
                color: #777;
            }
        }
    }
}

.featuresList {
    margin: 24px 0 0 0;
    padding: 0 0 0 4px;
    list-style: none;
    font-size: 14px;
    line-height: 24px;

    > li {
        margin: 0 0 8px 0;
        padding: 0 0 0 23px;
        position: relative;

        &:last-of-type {
            margin: 0;
        }

        .checkIcon {
            width: 16px;
            height: 16px;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #2979FF;
            position: absolute;
            top: 4px;
            left: 0;
        }
    }
}

.footerCta {
    position: sticky;
    width: calc(100% + 32px);
    margin: 100px 0 0 -16px;
    padding: 16px;
    background-color: #fff;
    bottom: 0;
    left: 0;
    box-shadow: 0 0 50px rgba(255,255,255,1), 0 0 50px rgba(255,255,255,1), 0 0 50px rgba(255,255,255,1);
    border-radius: 0 0 10px 10px;
    overflow: hidden;

    .terms {
        margin: 16px 0 0 0;
        font-weight: 500;
        font-size: 12px;
        line-height: 22px;
        color: rgba(0, 0, 0, 0.5);
        text-align: center;

        a {
            display: inline-block;
            color: rgba(0, 0, 0, 0.4);
            text-decoration: underline;
            margin: 0 4px;
        }
    }
}
