.root {
    display: flex;
    align-items: center;
    padding: 10px;
    border-radius: 10px;
    background-color: #f2f2f7;
}

.disabled {
    opacity: 0.4;
    pointer-events: none;
}

.interactive {
    cursor: pointer;
}

.hasMore {
    padding-right: 48px;
}

.icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 30px;
    height: 30px;
    border-radius: 4px;
    background-color: #fff;
    margin: 0 4px 0 0;

    &.large {
        width: 40px;
        height: 40px;
    }
}

.box {
    width: calc(100% - 34px);
    display: flex;
    align-items: center;

    &.withSubLabel {
        width: calc(100% - 44px);
    }
}

.label {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0 6px;
    flex: 1;
}

.title,
.subTitle {
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.title {
    font-weight: 500;
    font-size: 16px;
    line-height: 1.2;
}

.subTitle {
    margin: 1px 0 0 0;
    font-weight: normal;
    font-size: 13px;
    line-height: 1.2;
    color: #777;
}

.preview {
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: normal;
    font-size: 14px;
    color: #777;
    text-align: right;
    white-space: nowrap;
    flex-shrink: 0;
    max-width: 50%;
}

.imageBox {
    display: inline-block;
    vertical-align: top;
    width: 30px;
    height: 30px;
    border-radius: 4px;
    overflow: hidden;

    > img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.list {
    margin: 0;
    padding: 0;
    list-style: none;

    li {
        position: relative;
    }

    li + li {
        margin-top: 16px;
    }
}

.iconSplit {
    position: relative;

    &::before {
        position: absolute;
        left: 46px;
        content: "";
        height: 100%;
        width: 1px;
        border-style: solid;
        border-color: #efefef;
        border-width: 0 1px 0 0;
    }

    .icon {
        background: none;
    }

    .label {
        padding-left: 16px;
    }
}

.more {
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    align-items: center;
    display: inline-flex;
    padding: 0 16px;
}
