.root {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.header {
    text-align: center;
    padding: 0 16px;

    > h1 {
        font-weight: 700;
        font-size: 34px;
        line-height: 51px;
        margin: 0 0 4px 0;
    }

    > p {
        line-height: 22px;
        margin: 0;
    }
}

.tabs {
    padding: 16px;
    flex-shrink: 0;

    .tabsSelector li {
        border: none;
    }

    .tab {
        border-radius: 100px;
        background: #F2F2F7;
        padding: 8px 16px;
        font-weight: 600;
        font-size: 16px;
        color: #333;
        transition: background 0.3s ease, color 0.3s ease;

        &.active {
            background: #007AFF;
            color: #fff;
        }
    }
}

.slider {
    width: 100%;
    height: 100%;
}

.swiper {
    width: 100%;
    height: 100%;
    overflow: visible;

    .slideContent {
        width: 100%;
        height: 100%;
        min-height: 100%;
        padding: 16px 0 27px 0;
    }
}

.slideWrapper {
    width: 100%;
    height: 100%;

    .pageWrapper {
        overflow: hidden;
        transform-origin: 50% 0;
        border-radius: 20px;
        box-shadow: 0 43px 57px rgba(0, 0, 0, 0.1), 0 28px 43px rgba(0, 0, 0, 0.1), 0 7px 14px rgba(0, 0, 0, 0.1), 0 0 2px rgba(0, 0, 0, 0.1);
        transition: transform 0.3s ease, transform-origin 0.3s ease;

        .page {
            width: 100%;
            padding-top: 186%;
            box-shadow: none;
            border-radius: 0;
            pointer-events: none;
            position: relative;
        }

        .pageThumbnail {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: auto;
        }
    }
}

.footer {
    flex-shrink: 0;
    padding: 0 16px 16px 16px;
    position: relative;
    z-index: 1;

    .button {
        height: 56px;
        font-size: 16px;
        font-weight: 600;
        border-radius: 14px;
    }
}
