@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.imageUploadContainer {
    position: relative;

    .imagesBlock {
        width: 100%;
    }
}

.imagesList {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style: none;
}

.imagePreview {
    width: calc(33% - 10px);
    margin: 0 16px 6px 0;
    padding: 10px 0 0 0;
    position: relative;
    list-style: none;
    cursor: grab;
    pointer-events: auto !important;

    &.sortable {
        cursor: grabbing;

        .removePhotoBtn,
        .mainImageIcon {
            display: none;
        }
    }

    &:nth-of-type(3n) {
        margin: 0 0 6px 0;
    }

    .imagePreviewContainer {
        width: 100%;
        padding-top: 100%;
        position: relative;

        > img {
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 4px;
        }
    }

    .mainImageIcon {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        pointer-events: none;
        padding: 10px 0 0 0;
    }
}

.removePhotoBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    overflow: hidden;
    background-color: #f9f9f9;
    cursor: pointer;
    position: absolute;
    top: 0;
    right: -10px;
    box-shadow: inset 0 0 0 1px #ccc;

    > svg {
        transform: rotate(45deg);
        pointer-events: none;
    }
}

.addPhotoBtn {
    width: 100%;
    padding-top: 100%;
    position: relative;
    color: $link-color;
    background-color: $background-light;
    border-radius: 4px;
    overflow: hidden;
    cursor: pointer;

    .addPhotoBtnInput {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        cursor: pointer;
        opacity: 0;
        z-index: 1;
    }

    .addPhotoBtnContent {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;

        .addPhotoBtnPlus {
            display: flex;
            flex-direction: column;
            align-items: center;
            font-size: 12px;
            line-height: 24px;
            text-align: center;
        }
    }
}
