.root {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.closeBackButton {
    width: 50px;
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    cursor: pointer;

    svg {
        display: block;
    }
}

.slidesContainer {
    width: 100%;
    height: 100%;
    flex-shrink: 1;
    overflow: hidden;
}

.slidesList {
    height: 100%;
    display: flex;
    transition: transform 0.3s ease;
}

.slideItem {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.sliderBullets {
    list-style: none;
    margin: -16px 0 0 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;
    flex-shrink: 0;

    > li {
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: #C7C7C7;
        margin: 0 4px;
        transition: background 0.3s ease;

        &.active {
            background: #007AFF;
        }
    }
}

.actions {
    flex-shrink: 0;
    padding: 36px 16px 16px 16px;
    margin: auto 0 0 0;
}
