.root {
    width: 100%;
    padding: 10px;
    border-radius: 10px;
    background-color: #f5f5f5;
    text-align: left;
    color: #777;
    font-size: 14px;
    line-height: 1.4;

    p {
        margin: 0 0 10px 0;
    }

    > *:last-child {
        margin: 0;
    }

    .title {
        display: flex;
        width: 100%;
        font-weight: 500;
        letter-spacing: 0.4px;
        margin: 0 0 8px 0;
        color: #fff;
        justify-content: space-between;

        > span {
            display: inline-block;
            padding: 3px 9px;
            border-radius: 7px;
            background-color: #aaa;
        }

        .close {
            width: 24px;
            height: 24px;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #aaa;
            color: #fff;
            opacity: 0.6;
            transition: opacity 0.3s ease;
            position: relative;

            > svg {
                display: block;
            }

            &:hover {
                opacity: 0.8;
            }
        }
    }

    &.warning {
        background-color: #fff3cd;

        .title > span {
            background-color: #333;
        }
    }
}
