@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    background: $background-light;

    .list {
        display: block;
        width: 100%;
        margin: 0;
        padding: 0;
        list-style: none;
    }

    .filter {
        background: #fff;
        margin-top: 8px;
        padding: 16px;
        position: sticky;
        bottom: 0;
        z-index: 1;
        border-bottom: 1px solid $background-light;
    }
}

.groupItem,
.listItem {
    list-style: none;
    padding: 0;
    margin: 8px 0 0 0;
    display: flex;
    width: 100%;
    background-color: #fff;
}

.listItem {
    cursor: pointer;

    > * {
        max-width: 100%;
        padding: 14px 16px;
        flex: 1;
        position: relative;
        font-weight: normal;
        display: flex;
        align-items: center;
        font-size: 14px;
        color: $font-default-color;
        transition: color 0.3s ease;

        //@media all and (min-width: $tablet-landscape-breakpoint) {
        //    &:hover {
        //        color: $link-color;
        //    }
        //}

        .info {
            display: flex;
            flex-direction: column;
        }

        .title {
            display: block;
            flex: 1;
            @include crop();
        }

        .subtitle {
            font-size: 13px;
            color: #8e8e8e;
            user-select: none;
        }
    }

    &.fixed {
        opacity: 0.6;
        pointer-events: none;
    }
}
