.label {
    display: flex;
    width: 100%;
    overflow: hidden;
    align-items: center;

    > span {
        display: block;

        &:first-of-type {
            flex-shrink: 1;
            overflow: hidden;
            text-overflow: ellipsis;
            padding: 0 5px 0 0;
        }
    }
}

.icon {
    display: flex;
    flex-shrink: 0;
    padding: 0 10px 0 0;
    justify-content: center;
    align-items: center;

    > svg {
        display: block;
    }
}
