@import '@mssgme/scss-basics/mixins/crop';

.label {
    flex: 1;
}

.details {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    gap: 16px;
    column-gap: 16px;
    max-width: 50%;
}

.slug {
    opacity: 0.4;
    font-weight: 400;
    font-size: 80%;
    @include crop();
}
