@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.linkEditor {
    position: absolute;
    z-index: 100;
    top: 0;
    left: 0;
    margin-top: -6px;
    max-width: 300px;
    width: 100%;
    opacity: 0;
    background-color: $background-color;
    box-shadow: 0 5px 10px $rough-shadow-color;
    border-radius: 15px;
    transition: opacity 0.5s;

    &:focus {
        outline: none;
    }
}

.linkInput {
    margin: 8px;
    border-radius: 12px;
    background-color: darken($background-color, 5%);
    color: darken($font-default-color, 50%);
    font-size: 15px;

    input {
        border-width: 0 !important;
    }
}

.linkEdit {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35px;
    vertical-align: -0.25em;
    margin-right: -8px;
    cursor: pointer;
    border: none;
}

.linkEditor:focus .linkEdit svg {
    animation: pulseButton 2s linear infinite;
}

@keyframes pulseButton {
    0% {
        color: rgb(0, 0, 0);
    }
    50% {
        color: rgb(50, 100, 255);
    }
    100% {
        color: rgb(0, 0, 0);
    }
}
