@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    display: block;
    width: 100%;
}

.fetching {
    pointer-events: none;
}

.list {
    display: block;
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
}

.listItem {
    list-style: none;
    padding: 0;
    margin: 8px 0 0 0;
    display: flex;
    width: 100%;
    background-color: #fff;

    &:first-of-type {
        margin: 0;
    }

    > a {
        max-width: 100%;
        padding: 14px 16px;
        flex: 1;
        position: relative;
        font-weight: normal;
        display: flex;
        align-items: center;
        font-size: 14px;
        color: $font-default-color;
        transition: color 0.3s ease;

        @media all and (min-width: $tablet-landscape-breakpoint) {
            &:hover {
                color: $link-color;
            }
        }

        .info {
            display: flex;
            flex-direction: column;
        }

        .label {
            font-weight: bold;
            flex: 1;
            @include crop();
        }
    }

    &.draggable {
        > a {
            max-width: calc(100% - 40px);
            padding: 14px 8px 14px 16px;
        }
    }

    &.fixed {
        opacity: 0.6;
        pointer-events: none;
    }
}

.hiddenItem {
    display: none;
}

.status {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #ccc;
    margin: 0 10px 0 0;
    pointer-events: none;

    &.active {
        background-color: #3fb990;
    }
}

.label {
    display: block;
    flex: 1;
    @include crop();
}

.sortable {
    display: flex;
    list-style: none;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.sortableList {
    transition: opacity 0.3s ease;

    &.isFetching {
        opacity: 0.6;
        pointer-events: none;
    }
}

.counter {
    font-size: 13px;
    color: #8e8e8e;
}
