.root {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 36px;
    overflow: hidden;
    position: relative;
    border-radius: 6px;
    cursor: pointer;
    font-size: 14px;
    color: #fff;
    box-shadow: 0 1.5px 20px 0 rgba(0, 0, 0, 0.08);

    &.compact {
        display: block;
        width: 100px;
        height: auto;
        text-align: center;
        padding: 10px;
        margin: 0 auto;

        .icon {
            width: 100%;
            margin: 0 0 4px 0;
        }
    }
}

.icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 100%;
    flex-shrink: 0;
}

.label {
    width: 100%;
    flex: 1;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
