@import '@mssgme/scss-basics/vars';

.root {
    display: flex;
    width: 100%;
    flex-direction: column;
    min-height: calc(100% - 88px);
    position: relative;
    padding: 0 0 16px 0;
    background-color: #f7f7fa;

    .container {
        display: flex;
        width: 100%;
        flex-direction: column;
        padding: 0 16px 0 16px;
        position: relative;
        margin: 0;
        flex: 1;
        align-items: center;
    }

    &.withTitle {
        background-color: #fff;

        .container {
            margin-top: -41px;
        }
    }
}
