.root {
    display: block;
}

.header {
    margin: -56px 0 24px 0;
    text-align: center;

    .logo {
        display: block;
        width: 72px;
        height: auto;
        margin: 0 auto;
    }

    .heading {
        font-weight: 700;
        font-size: 34px;
        line-height: 40px;
        margin: 10px 0 0 0;
        text-align: center;
    }

    .subheading {
        font-size: 16px;
        line-height: 19px;
        margin: 16px 0 24px 0;
    }
}

.list {
    width: 100%;
    list-style: none;
    margin: 0 0 16px 0;
    border-radius: 12px;
    background-color: #f7f7fa;
    padding: 6px 16px;

    > li {
        padding: 10px 0;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;

        &:not(:last-of-type)::after {
            content: '';
            display: block;
            width: 100%;
            height: 1px;
            position: absolute;
            bottom: -1px;
            left: 0;
            background-color: #eee;
        }

        > .key {
            font-weight: 600;
            display: block;
            width: 45%;
            padding: 0 5px 0 0;
            white-space: pre-line;
            text-align: left;
        }

        > .value {
            width: 55%;
            padding: 0 0 0 5px;
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-end;
            align-items: center;
            text-align: right;
        }
    }
}

.buttonsContainer {
    > *:not(:last-child) {
        margin-bottom: 8px;
    }
}
