@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.wrapped {
    display: block;
    width: 100%;
    height: 100%;
    padding: 10px 14px;
    background-color: #f7f7fa;
    color: #333;
    border-radius: 12px;
    overflow: hidden;
}

.tags,
.notes {
    padding: 6px 0 0 0;
    font-size: 14px;
    color: darken(#b4b4bb, 10%);
    margin: 8px 0 0 0;
    border-top: 1px solid rgba(0, 0, 0, 0.08);
    display: flex;
    align-items: center;
    column-gap: 7px;
    width: 100%;

    > svg {
        display: block;
        margin-top: 1px;
        flex-shrink: 0;
    }

    > span {
        display: block;
        @include cropLines($lines: 1);
    }
}

.header {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;

    > span:first-of-type {
        display: block;
        font-weight: bold;
        font-size: 20px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    > span:last-of-type {
        display: block;
        font-size: 14px;
        color: #b4b4bb;
        flex-shrink: 0;
    }
}

.content {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.list {
    width: 100%;
    overflow: hidden;
    list-style: none;
    margin: 0;
    padding: 0;
    font-size: 14px;
    line-height: 18px;
    color: #b4b4bb;
    font-weight: normal;

    > li {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        &:not(:last-of-type) {
            margin: 0 0 1px 0;
        }
    }
}

.contentRight {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    gap: 8px;
    align-items: flex-end;
}

.status {
    font-weight: bold;
    font-size: 14px;
    line-height: 1;

    &.new {
        color: $status-new;
    }

    &.processing {
        color: $status-processing;
    }

    &.accepted {
        color: $status-accepted;
    }

    &.rejected {
        color: $status-rejected;
    }
}

.total {
    font-weight: normal;
}

.tags {
    padding-top: 6px !important;
    margin-top: 8px !important;

    > li {
        font-size: 14px;
        padding: 0;
    }
}
