.list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.item {
    margin: 0;
    padding: 8px 0;
    border-bottom: 1px solid #efefef;

    &:last-child {
        border-bottom: none;
    }
}

.details {
    display: flex;
    flex-direction: row;
    font-size: 80%;
    row-gap: 8px;

    > span + span:before {
        content: ', ';
    }
}

.name {
    font-weight: 700;
    padding: 0 0 8px 0;
}

.more {
    margin-top: 16px;
}

.dateSection {
    position: sticky;
    text-align: center;
    font-weight: bold;
    padding: 6px 0;
}

.emptyMessage {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 16px;
    text-align: center;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}
