@import '@mssgme/scss-basics/vars';

.selectionRanges {
    pointer-events: none;
    position: absolute;
    left: 0;
    top: 0;

    opacity: 1;
    transition: opacity $quick-transition;
    will-change: opacity;

    &.disabled {
        opacity: 0;
    }
}

.selectionArea {
    position: absolute;

    outline: 2px solid $link-color3;
    outline-offset: 0;

    transition: top $x-quick-transition, height $x-quick-transition;
    will-change: top, height;
}
