@import '@mssgme/scss-basics/vars';

.intro {
    text-align: center;
    margin: 0 0 30px 0;

    > h1 {
        font-weight: 700;
        font-size: 28px;
        margin: 0;
    }

    > p {
        margin: 12px 0 0 0;
    }
}

.currencyBadgeWrapper {
    flex: 0 0 90px;
    display: flex;
    justify-content: space-between;

    &.lax {
        flex: 0;

        .currencyCode {
            margin-left: 8px;
        }
    }
}

.currencySeparator {
    margin: 0 4px;
}

.colsGroup {
    flex: 1;
    padding: 8px 16px;
    font-weight: bold;
    background: $background-light;
}
