@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

$hover: rgba(223, 232, 250, 0.3);
$active: #eee;

.toolbarItem {
    position: relative;
    border: 0;
    display: flex;
    background: none;
    border-radius: 10px;
    padding: 8px;
    cursor: pointer;
    vertical-align: middle;
    align-items: center;
    flex: 1;

    &.small {
        padding: 4px;
    }

    &:first-child {
        margin-left: 0;
    }

    &.active {
        background-color: $active !important; // !important to override hover style

        > i {
            opacity: 1;
        }
    }

    @media (hover: hover), (pointer: fine) {
        &:hover:not([disabled]) {
            background-color: $hover;
        }
    }
}

.icon {
    background-size: contain;
    display: flex;
    height: 18px;
    width: 18px;
    font-size: 18px;
    user-select: none;
    margin: 2px;
    line-height: 1;
}

.text {
    display: inline-block;
    line-height: 24px;
    vertical-align: middle;
    font-size: 14px;
    color: $font-default-color;
    text-align: left;
    min-width: 0;

    @include crop();
}

.badge {
    position: absolute;
    top: 0;
    right: 13px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 8px;
    line-height: 6px;
    padding: 0 2px;
    min-height: 12px;
    margin: 2px 0 3px 0;
}

.fixedSize {
    .icon {
        opacity: 0.6;
        margin-right: 8px;
    }

    .text {
        width: 70px;
    }
}

.toolbarItem:disabled,
.disabled {
    pointer-events: none;

    .icon,
    .text {
        opacity: 0.5;
    }
}

.small {
    .icon {
        height: 16px;
        width: 16px;
        font-size: 16px;
    }

    .text {
        height: 16px;
        line-height: 16px;
    }
}
