@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.infoLine {
    display: flex;
    flex-direction: column;
    font-weight: 500;
    line-height: 16px;

    & + & {
        margin-top: 12px;
    }
}

.prop {
    color: $font-faded-color;
    font-size: 14px;
    margin-bottom: 4px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.value {
    color: $font-default-color;
    line-height: 1.2;

    > * {
        font-weight: 400;
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    &.spread > * {
        justify-content: space-between;

        > svg {
            display: block;
            flex-shrink: 0;
        }
    }
}

.divider {
    margin: 16px 0;
    width: 100%;
    border-bottom: 1px solid $background-light;
}

.statusContainer {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
    left: 0;
    padding: 0;
    margin-top: -29px;
}

.statusSelect {
    border-radius: 100px;
    font-size: 14px;
    line-height: 16px;
    margin: 0 auto;
    flex: 0;
}

.dataView {
    // this is necessarily nested
    // todo: research, why built css in production have different classes declaration order from dev builds,
    //  probably, should enforce import order in style-loader/css-loader

    .copyContainer {
        display: flex;
        flex-direction: row-reverse;
        background: none;
        padding: 2px 0 0 0;
        border-radius: 0;
    }

    .copyTag {
        display: flex;
        flex-direction: row;
        padding: 0;
    }

    .copyButton {
        padding: 2px 0;
        align-items: start;
    }
}

.dataContainer {
    flex: 1;
    white-space: pre-wrap;
    word-break: break-word;

    &.cut {
        cursor: pointer;
    }
}

.paymentStatus {
    margin: 0;
    min-height: 0;

    &.pending {
        color: $status-processing;
    }
    &.paid {
        color: $status-accepted;
    }
    &.failed {
        color: $status-rejected;
    }
}

.paymentStatusSelect {
    display: flex;
    flex-direction: column;
}

.choice {
    border: 0;
    padding: 8px;
    margin: 0;

    &.active {
        color: $font-default-color;
        background: none;
    }
}

.choiceLabel {
    display: flex;
    gap: 10px;
    align-items: center;
}

.tags {
    margin: 0;
    padding: 0;
    list-style: none;
    flex-wrap: wrap;
    row-gap: 8px;
    column-gap: 8px;
}

.tag {
    background: $background-light;
    border-radius: 8px;
    padding: 2px 6px;
    white-space: nowrap;
}
