.container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.saturationPicker {
    flex: 1;
    height: 146px;
    position: relative;
    margin: 0 16px;

    div {
        border-radius: 8px;
    }
}

.verticalPicker {
    > div {
        border-radius: 6px !important;
        box-shadow: 0 0 0 1px #eee;

        > div {
            border-radius: 6px !important;
        }
    }
}

.inputContainer {
    width: 100%;
    flex-shrink: 0;
    margin: 20px 0 16px 0;

    input {
        transition: background 0.3s ease;

        &:focus {
            background: #e5f6ff !important;
        }
    }
}
