.slideHeader {
    padding: 0 16px;
    user-select: none;
    text-align: center;
}

.title {
    font-weight: 700;
    font-size: 34px;
    line-height: 1.5;
    margin: 0;
}

.subTitle {
    margin: 4px 0 0 0;
    line-height: 1.4;
    white-space: pre-line;
}

.slideContent {
    overflow: hidden;
    flex-shrink: 1;
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
    padding: 36px 16px 46px 16px;

    img,
    video {
        display: block;
        height: 600px;
        max-height: 100%;
        width: auto;
        user-select: none;
        border-radius: 20px;
        box-shadow: 0 24px 32px rgba(0, 0, 0, 0.1),
        0 16px 24px rgba(0, 0, 0, 0.1),
        0 4px 8px rgba(0, 0, 0, 0.1),
        0 0 1px rgba(0, 0, 0, 0.1);
    }
}
