.content {
    ol {
        margin: 0;
        padding: 0 0 0 18px;

        > li {
            margin: 0 0 12px 0;

            &:only-of-type,
            &:last-of-type {
                margin: 0;
            }

            b {
                font-weight: 500;
            }
        }
    }

    .tipImg {
        display: block;
        max-width: 100%;
        width: 343px;
        height: 296px;
        object-fit: contain;
        margin: 16px auto;
    }
}

.userLink {
    margin: 12px 0;
}
