@import '@mssgme/scss-basics/vars';

.root {
    ul {
        list-style: none;
        padding: 0;
        margin: 0;
        border-radius: 10px;
        display: flex;
        gap: 8px;
        flex-wrap: wrap;
    }

    .badge {
        cursor: pointer;
        border-radius: 24px;
        background: #EDEDF0;
        color: #000;
        font-size: 12px;
        display: flex;
        padding: 0;

        span {
            padding: 4px 10px;
        }

        .badgeButton {
            width: 16px;
            height: 16px;
            display: flex;
            margin: 4px 4px 4px -4px;
            vertical-align: baseline;
            border-radius: 16px;
            align-items: center;
            justify-content: center;

            color: #333;
            background-color: transparent;
            transition: color 0.2s, background-color 0.2s;

            &:hover {
                background-color: $danger;
                color: #fff;
            }
        }
    }

    &.disabled {
        .badge {
            opacity: 0.3;
            pointer-events: none;
        }
    }
}

.changeButton {
    min-height: unset;
    height: 32px;

}
