@import '@mssgme/scss-basics/vars';
.root {
    div.tabs {
        display: flex;
        border-radius: 10px;
        background-color: #f7f7fa;
        padding: 5px;

        > div {
            flex: 1;
            border: none;
            text-align: center;
            font-weight: 500;

            &:first-of-type {
                padding-left: 0;
            }

            &:last-of-type {
                padding-right: 0;
            }

            > span {
                border-radius: 8px;
            }
        }
    }
}

.giphyComponents {
    .carousel {
        width: calc(100% + 32px);
        height: 110px;
        margin: 0 0 0 -16px;
        padding: 0 16px;

        &::-webkit-scrollbar {
            width: 0;
            height: 4px;
        }

        &::-webkit-scrollbar-track {
            background: rgba(255, 255, 255, 0);
        }

        &::-webkit-scrollbar-thumb {
            background: #ccc;
            border-radius: 2px;
        }

        @media all and (max-width: $mobile-breakpoint) {
            height: 100px;

            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }

            &::-webkit-scrollbar-thumb {
                background: rgba(255, 255, 255, 0);
            }
        }

        > div {
            cursor: pointer;
        }
    }

    .searchInput {
        background: #f5f5f5;
        border-radius: 12px;
        overflow: hidden;
        margin: 12px 0 0 0;

        > input {
            background: #f5f5f5;
            padding: 0 16px;
            font-size: 16px;
        }

        svg {
            width: 45%;
            height: 45%;
        }
    }
}

.noResults {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
}

.giphyAttribution {
    display: block;
    height: 16px;
    width: auto;
    margin: 16px auto 0 auto;
}
