.domainPreview {
    .label {
        display: block;
        padding: 0 10px;
        margin: 0 0 4px 0;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
    }

    .preview {
        display: flex;
        width: 100%;
        min-height: 38px;
        justify-content: space-between;
        align-items: center;
        padding: 0 16px;
        background-color: #eee;
        border-radius: 10px;

        .value {
            flex: 1;
            flex-shrink: 0;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;

            > span {
                flex: 1;
                flex-shrink: 0;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                display: block;
                font-weight: normal;

                &.empty {
                    color: #8e8e93;
                }
            }
        }

        .after {
            height: 100%;
            flex-shrink: 1;
            padding: 0 0 0 10px;
            font-weight: 500;
            font-size: 15px;

            > a {
                display: block;
            }
        }
    }
}
