:global(#mssgme_widget).animated {
    & > :global(#mssg_widget_box), & > :global(#widget_popup), & > :global(#widget_circle) {
        transition: opacity 0.3s ease;
    }
}

:global(#mssgme_widget).hidden {
    & > :global(#mssg_widget_box), & > :global(#widget_popup), & > :global(#widget_circle) {
        opacity: 0 !important;
        pointer-events: none !important;
    }
}
