.iconsContainer {
    display: flex;
    flex-direction: row !important;
    flex-wrap: wrap;

    .choiceButton {
        width: calc(16.666666% - 13.5px);
        padding: 0;
        margin: 0 16px 16px 0;
        border-radius: 10px;
        background-color: #f5f5f5;
        position: relative;

        &:nth-of-type(6n) {
            margin: 0 0 16px 0;
        }

        &.active {
            background-color: #00adff;
            color: #fff;

            svg {
                color: #fff !important;
            }
        }

        > div:nth-of-type(2) {
            display: none;
        }

        &.fullWidth {
            width: 100%;
            margin: 0;
        }
    }

    .groupLabel {
        display: flex;
        justify-content: center;
    }

    .iconPreview {
        width: 100%;
        padding-top: 100%;
        position: relative;
    }

    .iconNoIcon {
        padding: 7px 16px;
        text-align: center;
    }

    .iconWrapper {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        left: 0;
    }
}
