@import '@mssgme/scss-basics/vars';

.root {
    width: calc(100% + 32px);
    overflow: hidden;
    margin: 0 0 -16px -16px;
    position: relative;
    font-size: 16px;
    font-weight: lighter;

    .item {
        font-weight: 500;
        text-align: center;
        display: block;
        flex-grow: 1;
        margin: 0 16px 0 0;
        user-select: none;
        border-bottom: 2px solid #fff;
        cursor: pointer;
        color: rgba(0, 0, 0, 0.5);
        transition: color 0.3s ease, border-color 0.3s ease;

        > * {
            overflow: hidden;
            padding: 7px 0;
            display: block;
            position: relative;
            white-space: nowrap;
            background-color: #fff;
        }

        &.active {
            color: #333;
            border-color: #2979ff;
        }

        &:last-of-type {
            margin: 0;
        }

        @media all and (min-width: 768px) {
            &:hover {
                color: #333;
            }
        }
    }
}

.list {
    width: 100%;
    display: flex;
    overflow: auto;
    list-style: none;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;
    font-size: 14px;

    &::before,
    &::after {
        content: '';
        display: block;
        padding: 4px 8px;
    }
}

.list::-webkit-scrollbar {
    width: 0;
    height: 0;
}
.list::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0);
}
.list::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0);
}

.controlContainerLeft,
.controlContainerRight {
    height: 100%;
    width: 44px;
    position: absolute;
    top: 0;
    z-index: 1;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    user-select: none;

    .control {
        width: 28px;
        height: 28px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: bold;
        transition: transform 0.3s ease;
    }
}

.controlContainerLeft {
    left: 0;
    background-image: linear-gradient(90deg, currentColor 0%, currentColor 45%, rgba(255, 255, 255, 0) 100%),
        linear-gradient(90deg, currentColor 0%, currentColor 45%, rgba(255, 255, 255, 0) 100%),
        linear-gradient(90deg, currentColor 0%, currentColor 45%, rgba(255, 255, 255, 0) 100%);

    .control {
        &:hover {
            transform: translateX(-3px);
        }
    }
}

.controlContainerRight {
    right: 0;
    background-image: linear-gradient(-90deg, currentColor 0%, currentColor 45%, rgba(255, 255, 255, 0) 100%),
        linear-gradient(-90deg, currentColor 0%, currentColor 45%, rgba(255, 255, 255, 0) 100%),
        linear-gradient(-90deg, currentColor 0%, currentColor 45%, rgba(255, 255, 255, 0) 100%);

    .control {
        &:hover {
            transform: translateX(3px);
        }
    }
}
