@import '@mssgme/scss-basics/vars';

.tabs {
    flex-wrap: nowrap;
    white-space: nowrap;
    margin-bottom: -22px;
}

.choice {
    display: flex;
    border: none;
    border-radius: 0;
    padding: 6px 8px 6px 0;
    margin: 0;
    background-color: transparent;
    font-size: 10px;

    color: rgba(0, 0, 0, 0.5);

    & + &:before {
        content: '';
        border-left: 1px solid rgba(0, 0, 0, 0.1);
        margin: auto 8px auto 0;
        height: 12px;
    }
}

.tab {
    display: flex !important;
    align-items: center;
}

.counter {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 0 0 5px;
    font-size: 11px;
    line-height: 1;
    min-height: 17px;
    min-width: 17px;
    color: #888;
    padding: 0 4px;
    background-color: #f0f0f0;
    position: relative;
    top: 1px;
    border-radius: 4px;

    > div {
        margin-left: -1px;
    }
}
