.root {
    padding: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 500px;
    margin: 0 auto;
    height: 100vh;
}

.heading {
    text-align: center;
}

.content {
    width: 100%;
}

.code {
    overflow: hidden;
    word-wrap: break-word;
}
