@import '@mssgme/scss-basics/vars';
@import '@mssgme/scss-basics/mixins/crop';

.root {
    position: relative;
    z-index: 1;
    height: 24px;
    user-select: none;
    display: inline-block;

    background-color: $font-default-color;
    color: $background-color;

    &.new {
        background-color: $status-new;
    }
    &.processing {
        background-color: $status-processing;
    }
    &.accepted {
        background-color: $status-accepted;
    }
    &.rejected {
        background-color: $status-rejected;
    }

    @media all and (max-width: $mobile-breakpoint) {
        height: 26px;
    }

    &.disabled {
        pointer-events: none;
        opacity: 0.3;
    }
}

.toggle {
    display: flex;
    position: relative;
    width: 100%;
    height: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 0 6px 0 16px;
    border-radius: 100px;
    z-index: 2;
    cursor: pointer;
    gap: 6px;
    row-gap: 6px;

    @media all and (max-width: $mobile-breakpoint) {
        padding-top: 0;
    }
}

.label {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    margin: 0;
    flex: 1;
    @include crop();
}

.menu {
    min-width: calc(100% - 20px);
    position: absolute;
    top: 100%;
    left: 0;
    margin: 0;
    list-style: none;
    padding: 0;
    background-color: #fff;
    color: #333;
    z-index: 1;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    max-height: calc(100vh - 56px);
    user-select: none;
    border: 1px solid #333;
    border-radius: 4px;

    &.visible {
        transform: none;
    }

    .option {
        white-space: nowrap;
        padding: 8px 16px 8px 4px;

        &.active,
        &:hover,
        &:focus {
            background-color: $link-color3;
            color: $background-color;
        }

        & + .option {
            border-top: 1px solid $thin-line-color;
        }
    }
}
