.contentContainer {
    padding: 0 0 16px 0;
    margin: -12px 0 0 0;

    .heading {
        margin: 0 0 24px 0;
        text-align: center;

        > h1 {
            font-weight: 700;
            font-size: 34px;
            line-height: 40px;
            margin: 0;
        }

        > p {
            font-size: 16px;
            line-height: 19px;
            margin: 7px 0 0 0;
        }
    }

    .form {
        padding: 6px 16px;
        border-radius: 12px;
        background-color: #F7F7FA;
    }
}
