@import '@mssgme/scss-basics/vars';

.root {
    width: 40px;
    min-width: 40px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    cursor: grab;
    pointer-events: none;
    opacity: 0.08;
    user-select: none;
    color: $placeholder-color;
    transition: opacity $default-transition;

    &.active {
        pointer-events: auto;
        opacity: 1;
    }

    &.hidden {
        opacity: 0;
    }

    &.small {
        width: 16px;
        min-width: 16px;
    }

    &.medium {
        min-width: 32px;
        justify-content: center;
    }

    &.auto {
        width: auto;
        min-width: auto;
        padding: 0 16px;
        margin-left: -16px;
    }

    svg {
        height: auto;
        display: block;
        pointer-events: none;
        transition: color $default-transition;
    }

    @media all and (min-width: $tablet-landscape-breakpoint) {
        &:hover {
            svg {
                color: $link-color;
            }
        }
    }
}

.sortable {
    margin: 0;
    padding: 0;
    z-index: 10000;
    cursor: grabbing;
    pointer-events: auto !important;

    * {
        cursor: grabbing;
    }
}
