.root {
    width: 100%;
    height: 100%;
    border-radius: 8px;
    box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.12);
    overflow: hidden;
    display: flex;
    position: relative;

    .badgeTop {
        position: absolute;
        top: 5px;
        right: 5px;
        display: flex;

        .small {
            padding: 3px;
            font-size: 8px;
        }
    }

    img {
        display: block;
        width: 100%;
        height: auto;
        align-self: flex-start;
    }

    .innerLabel {
        display: flex;
        flex: 1;
        align-items: center;
        justify-content: center;
    }
}
