.contentContainer {
    padding: 0 0 16px 0;
}

.logo {
    margin: -35px 0 26px 0;

    > svg {
        display: block;
        width: 41px;
        height: auto;
        margin: 0 auto;
    }
}

.heading {
    margin: 0 0 24px 0;
    text-align: center;

    > h1 {
        font-weight: 700;
        font-size: 34px;
        line-height: 40px;
        margin: 0;
    }

    > p {
        font-size: 16px;
        line-height: 19px;
        margin: 16px 0 0 0;
    }
}

.order {
    border-radius: 12px;
    background-color: #F7F7FA;
    margin: 0 0 16px 0;
}

.orderList {
    list-style: none;
    margin: 0;
    padding: 6px 16px;
    font-size: 16px;

    > li {
        display: flex;
        width: 100%;
        justify-content: space-between;
        position: relative;
        padding: 10px 0;
    
        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 1px;
            position: absolute;
            bottom: -1px;
            left: 0;
            background-color: #eee;
        }
    
        &:last-of-type::after {
            display: none;
        }

        > span:last-of-type {
            font-weight: 600;
        }

        strong {
            display: inline-block;
        }
    }
}

.payForm {
    padding: 6px 16px;
    border-radius: 12px;
    background-color: #F7F7FA;
}
