.root {
    width: 100%;
    margin: -12px 0 0 0;
    padding: 0 0 24px 0;
}

.header {
    margin: 0 0 24px 0;
    text-align: center;

    > h1 {
        font-weight: 700;
        font-size: 34px;
        line-height: 40px;
        margin: 0;
    }

    > p {
        font-size: 16px;
        line-height: 22px;
        margin: 7px 0 0 0;
    }
}

.plansList {
    margin: 0;
    padding: 0;
    list-style: none;

    > li {
        margin: 0 0 16px 0;

        &:last-of-type {
            margin: 0;
        }
    }
}

.footer {
    margin: 24px 0 0 0;
    text-align: center;

    h2 {
        font-weight: normal;
        font-size: 16px;
        margin: 0 0 16px 0;
    }
}
