@import '@mssgme/scss-basics/vars';

.root {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 0;
}

.navItemWrapper {
    min-width: 21%;
    margin: 0 6px;

    &.en {
        min-width: 18%;
        margin: 0 18px;
    }
}

.navItem {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    color: #333;
    cursor: pointer;

    .navItemIcon svg:nth-child(2) {
        opacity: 0;
        transform: translate(-50%, -50%) scale(0);
    }

    &.button {
        pointer-events: auto;
    }

    &.copied {
        color: #07adff;
        pointer-events: none;

        .navItemIcon svg:nth-child(1) {
            opacity: 0;
            transform: translate(-50%, -50%) scale(0);
        }

        .navItemIcon svg:nth-child(2) {
            opacity: 1;
            transform: translate(-50%, -50%) scale(1);
        }
    }
}

.navItemIcon {
    width: 54px;
    height: 54px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.08);
    transition: transform 0.3s ease, color 0.3s ease;
    position: relative;

    > svg,
    > img {
        display: block;
        width: 22px;
        height: 22px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform-origin: 50% 50%;
        transform: translate(-50%, -50%);
        transition: transform 0.3s ease, opacity 0.3s ease;
    }
}

.navItemLabel {
    display: block;
    margin: 8px 0 0 0;
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    transition: color 0.3s ease;
}
