.root {
    margin: -16px 0 0 0;
    height: 100%;
    display: flex;
    flex-direction: column;

    .intro {
        text-align: center;

        .title {
            font-weight: 700;
            font-size: 34px;
            line-height: 51px;
            margin: 0;
        }

        .subHeading {
            margin: 4px 0 0 0;
            line-height: 22px;
        }
    }

    .pageContainer {
        flex: 1;
        overflow: hidden;
        padding: 16px 0 36px 0;

        .pageWrapper {
            display: block;
            width: 100%;
            height: 182%;
            margin: auto;
            transform-origin: 50% 0;
            border-radius: 20px;
            overflow: hidden;
            position: relative;
            box-shadow: 0 43px 57px rgba(0, 0, 0, 0.1), 0 28px 43px rgba(0, 0, 0, 0.1), 0 7px 14px rgba(0, 0, 0, 0.1), 0 0 2px rgba(0, 0, 0, 0.1);

            .pageWrapperLink {
                display: block;
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
            }
        }
    }

    .userLink {
        background: #f2f2f7;
        border-radius: 14px;
        overflow: hidden;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .status, .editBtn {
            display: flex;
            width: 56px;
            min-height: 56px;
            flex-shrink: 0;
            justify-content: center;
            align-items: center;
            color: #333;
        }

        .status::after {
            content: '';
            display: block;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background: #3fb990;
        }

        .link {
            display: block;
            width: 100%;
            text-align: center;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            color: #333;
            flex-shrink: 1;
        }
    }

    .actions {
        margin: 16px 0 0 0;

        .list {
            margin: 0 auto;
            padding: 0;
            list-style: none;
            display: flex;
            justify-content: space-between;
            width: 270px;
            max-width: 100%;

            .button {
                color: #333;
                text-align: center;

                .circle {
                    width: 56px;
                    height: 56px;
                    border-radius: 50%;
                    background: #f2f2f7;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: relative;
                    overflow: hidden;
                    margin: 0 auto;

                    .iconsSlider {
                        width: 100%;
                        height: 100%;
                        display: flex;
                        flex-shrink: 0;
                        position: absolute;
                        top: 0;
                        left: 0;

                        .icon {
                            width: 100%;
                            height: 100%;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            position: absolute;
                            opacity: 0;
                            transform: scale(0.6);
                            top: 0;
                            left: 0;
                            transition: transform .3s ease, opacity 0.3s ease;
                            animation: fadeInOut 6s ease infinite;

                            &:last-of-type {
                                animation-delay: 3s;
                            }
                        }
                    }
                }

                .title {
                    display: block;
                    font-size: 11px;
                    line-height: 13px;
                    margin: 8px 0 0 0;
                }
            }
        }
    }
}

@keyframes fadeInOut {
    0% {
        transform: scale(0.6);
        opacity: 0;
    }
    5% {
        transform: scale(1);
        opacity: 1;
    }
    45% {
        transform: scale(1);
        opacity: 1;
    }
    50% {
        transform: scale(0.6);
        opacity: 0;
    }
    100% {
        transform: scale(0.6);
        opacity: 0;
    }
}
