@import '@mssgme/scss-basics/vars';

.list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.item {
    background-color: #f2f2f7;
    border-radius: 10px;
    padding: 13px 16px;
    margin: 0 0 16px 0;
    transition: background-color $default-transition, outline-color $default-transition;
    cursor: pointer;
    outline: 2px solid transparent;
    outline-offset: 2px;

    &:last-of-type {
        margin: 0;
    }
}

.label {
    font-size: 18px;
    font-weight: bold;
    line-height: 1.2;
    margin: 0 0 5px 0;
}

.subLabel {
    font-size: 16px;
    color: #555;
    text-overflow: initial;
    white-space: normal;
    margin: 0;
}

.active {
    background-color: #e5f6ff;
    outline-color: $primary;
}
