.row {
    margin-bottom: 16px;
}

.addButton {
    padding: 0;
    font-size: 16px;
    margin-bottom: 16px;
}

.item {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 8px;
    background: #f7f7fa;
    height: 44px;
    padding: 0 12px;
    border-radius: 8px;
    user-select: none;

    .drag {
        width: auto;
        min-width: auto;
        padding: 0 16px;
        margin-left: -16px;
        color: #7c7c7d;
    }

    .itemContainer {
        display: flex;
        flex-grow: 1;
        height: 44px;
        align-items: center;
        width: 1%;

        .itemContentWrapper {
            display: flex;
            align-items: center;
            flex-grow: 1;
            overflow: hidden;
            font-weight: 500;
            line-height: 20px;
            cursor: pointer;
        }

        .itemContent {
            display: flex;
            flex: 1;
            align-items: center;
            overflow: hidden;
        }

        .itemText {
            flex-grow: 1;
            padding: 0 6px 0 10px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;

            &:first-child {
                padding: 0 6px 0 0;
            }
        }
    }
}

.saveButton {
    padding-right: 16px;
    height: 100%;
    font-size: 16px;
}

.moreContainer {
    display: flex;
    width: 60px;
    height: 100%;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.actionsContainer {
    margin: -16px 0;

    .actionsList {
        list-style: none;
        padding: 0;

        .actionItem {
            margin: 16px 0;

            .actionButton {
                cursor: pointer;
            }
        }
    }
}

.modalText {
    margin: 0;
    color: #777;
    text-align: center;
}

.modalButton {
    margin-top: 16px;
}
