@import '@mssgme/scss-basics/vars';

.button {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color: $font-default-color;
    transition: color $default-transition, transform $quicker-transition;
    padding: 0 24px;
    pointer-events: auto;
    opacity: 1;
    position: relative;

    &:hover,
    &:focus,
    &.activeButton {
        color: $link-color;
    }

    &:active {
        transform: scale(0.9);
    }

    > svg {
        display: block;
        width: 24px;
        height: 24px;
        min-width: 24px;
        min-height: 24px;
        position: relative;
        z-index: 1;
    }

    &.rounded::after {
        content: '';
        display: block;
        width: 44px;
        height: 44px;
        border-radius: 50%;
        background: #fff;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.08);
    }
}

.buttonWrapper {
    position: relative;
    height: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    user-select: none;
    overflow: hidden;
    transition: left $default-transition, min-width $default-transition, opacity $default-transition;
    width: 0;
    min-width: 0;
    opacity: 0;
    will-change: min-width, opacity;
}

.activeWrapper {
    min-width: 24px * 3;
    opacity: 1;
}

.disabled, .disabled .button {
    pointer-events: none;
    opacity: 0.4;
}

.title {
    font-size: 8px;
    margin-top: 6px;
    position: relative;
    z-index: 1;
    flex-wrap: nowrap;
    white-space: nowrap;
}

.badge {
    pointer-events: none;
    position: absolute;
    font-size: 8px;
    min-height: 5px;
    line-height: 5px;
    right: 4px;
    bottom: 18px;
}

.labeled {
    &.activeWrapper {
        min-width: 32px + 16px * 2;

        &.slim {
            min-width: 24px + 15px * 2;
        }
    }

    .button {
        padding: 0 8px;
    }
}

.centered {
    min-width: 32px + 32px * 2;

    .button {
        padding: 15px 8px 0 8px;
    }

    .title {
        margin-top: 2px;
    }
}
