.root {
    width: calc(100% + 32px);
    margin: 0 0 0 -16px;
    padding: 16px 16px 0 16px;
    border-top: 1px solid #f2f2f2;
}

.section {
    display: flex;
    width: 100%;
    overflow: auto hidden;
}

.fieldOptionsList {
    width: 100%;
    height: 24px;
    list-style: none;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 0 16px;
    border-right: 1px solid #b4b4bb;

    &:last-of-type {
        border: none;
        padding: 0 0 0 10px;
    }

    &:first-of-type {
        padding: 0 10px 0 0;
    }

    &:only-of-type {
        padding: 0;
        justify-content: space-between;

        > li {
            flex: 1 1 auto;
            align-items: center;
            justify-content: center;
        }

        > li:not(:last-of-type) {
            border-right: 1px solid #b4b4bb;
        }
    }

    > li {
        cursor: pointer;
        text-align: center;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin: 0 2px;

        svg {
            display: block;
            margin: 0 auto;
        }

        &:first-of-type {
            margin-left: 0;
        }

        &:last-of-type {
            margin-right: 0;
        }
    }
}
