.closeIcon {
    position: absolute;
    padding: 16px;
    left: 0;
    top: 0;
    cursor: pointer;

    > svg {
        display: block;
    }
}

.content {
    text-align: center;
    padding: 22px 0 0 0;
}

.contentIcon {
    font-size: 36px;
    margin: 0 0 20px 0;
    line-height: 1;
}

.title {
    font-weight: bold;
    font-size: 20px;
    line-height: 26px;
    margin: 0 0 12px 0;
}

.subtitle {
    margin: 0 0 30px 0;
}

.supportButton {
    margin: 0 0 8px 0;
}
